import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core';
import { HashRouter, Route } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Home from './pages/Home';
import { useTranslation } from 'react-i18next';
import NewDepositPage from './pages/NewDepositPage';
import ConnectWalletDialog from './components/ConnectWalletDialog';
import AppWalletWrapper from './components/AppWalletWrapper';
import AccountDialog from './components/AccountDialog';
import { loadCurrencies } from './actions/currencyActions';
import { connect } from 'react-redux';
import Bar from './components/layouts/Bar';
import CoinChooseDialog from './components/CoinChooseDialog';
import ExpandDepositPage from './pages/ExpandDepositPage';
import TransactionInProgressDialog from './components/TransactionInProgressDialog';
import StatusDialog from './components/StatusDialog';
import "./styles/styles.scss"

const useStyles = makeStyles(() => ({
  content: {
    marginTop: '80px',
  },
}));

function App(props) {
  const classes = useStyles();
  const { t } = useTranslation();
  const { loadCurrencies } = props;
  useEffect(() => {
    loadCurrencies();
  }, []);
  return (
    <AppWalletWrapper>
      <HashRouter>
        <Bar />
        <Grid container className={classes.content}>
          <Route exact path="/" component={Home} />
          <Route path="/new-deposit" component={NewDepositPage} />
          <Route path="/expand-deposit/:id" component={ExpandDepositPage} />
        </Grid>
        <ConnectWalletDialog />
        <AccountDialog />
        <TransactionInProgressDialog />
        <StatusDialog />
      </HashRouter>
    </AppWalletWrapper>
  );
}

const mapStateToProps = (state) => ({
  currencies: state.currencies,
});

const mapDispatchToProps = {
  loadCurrencies,
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
