import React from 'react';
import Jazzicon from 'react-jazzicon';
import { useWeb3React } from '@web3-react/core';
import { walletByConnector, walletIsMetaMask } from '../connectors';

const WalletIcon = ({ wallet }) => {
  return <img width={'32'} src={'/wallets/' + wallet.icon} alt={wallet.name} className={'d-inline-block fcx-wallet-icon'}/>;
};

const AddressesIdentIcon = () => {
  const { active, account, connector } = useWeb3React();
  const isMetaMask = walletIsMetaMask(connector);
  return (active ? isMetaMask ? <Jazzicon diameter={30} seed={parseInt(account.slice(2, 10), 16)}/> : <WalletIcon wallet={walletByConnector(connector)}/> : null);
};

export default AddressesIdentIcon;