import { useEffect, useState } from 'react';
import { DATA_REFRESH, PRICE_FETCHER_ABI_ADDRESS, PRICE_FETCHER_ABI_INTERFACE } from '../constants';
import { getProviderOrSigner } from '../utils';
import { Contract } from '@ethersproject/contracts';
import { formatUnits } from '@ethersproject/units';
import { useActiveWeb3React } from './index';

export function useTokenPriceUsd(tokenAddress) {

  const [price, setPrice] = useState(0.0);
  const [refreshInterval] = useState(DATA_REFRESH);

  const { account, chainId, library, active } = useActiveWeb3React();

  const fetchData = () => {
    if (active) {
      const contractPriceFetcher = new Contract(PRICE_FETCHER_ABI_ADDRESS[chainId], PRICE_FETCHER_ABI_INTERFACE, getProviderOrSigner(library, account));
      contractPriceFetcher.currentPrice(tokenAddress).then(res => {
        setPrice(parseFloat(formatUnits(res, 8)).toFixed(2));
      }).catch(e => {
        console.log(e);
        setPrice(0.0);
      });
    } else {
      setPrice(0.0);
    }
  };

  useEffect(() => {
    if (refreshInterval && refreshInterval > 0) {
      fetchData();
      const interval = setInterval(fetchData, refreshInterval);
      return () => clearInterval(interval);
    }
  }, [refreshInterval, tokenAddress, chainId, account, active]);
  return { price };
}
