export const CONNECT_A_WALLET_DIALOG_OPEN = 'Connect a wallet - open dialog';
export const CONNECT_A_WALLET_DIALOG_CLOSE = 'Connect a wallet - close dialog';

export const ACCOUNT_DIALOG_OPEN = 'Account - open dialog';
export const ACCOUNT_DIALOG_CLOSE = 'Account - close dialog';

export const CURRENCY_DIALOG_OPEN = 'CURRENCY_DIALOG_OPEN';
export const CURRENCY_DIALOG_CLOSE = 'CURRENCY_DIALOG_CLOSE';

export const TRANSACTION_DIALOG_OPEN = 'TRANSACTION_DIALOG_OPEN';
export const TRANSACTION_DIALOG_CLOSE = 'TRANSACTION_DIALOG_CLOSE';

export const STATUS_DIALOG_OPEN = 'STATUS_DIALOG_OPEN';
export const STATUS_DIALOG_CLOSE = 'STATUS_DIALOG_CLOSE';

export const STATUS_SUCCESS = 'OK';
export const STATUS_ERROR = 'ERROR';

export const openConnectAWalletDialog = () => {
  return {
    type: CONNECT_A_WALLET_DIALOG_OPEN,
    payload: true,
  };
};

export const closeConnectAWalletDialog = () => {
  return {
    type: CONNECT_A_WALLET_DIALOG_CLOSE,
    payload: false,
  };
};

export const openAccountDialog = () => {
  return {
    type: ACCOUNT_DIALOG_OPEN,
    payload: true,
  };
};

export const closeAccountDialog = () => {
  return {
    type: ACCOUNT_DIALOG_CLOSE,
    payload: false,
  };
};

export const openCurrencyDialog = () => {
  return {
    type: CURRENCY_DIALOG_OPEN,
    payload: true,
  };
};

export const closeCurrencyDialog = () => {
  return {
    type: CURRENCY_DIALOG_CLOSE,
    payload: false,
  };
};

export const openTransactionDialog = (transactionId) => {
  return {
    type: TRANSACTION_DIALOG_OPEN,
    payload: true,
    transactionId: transactionId,
  };
};

export const closeTransactionDialog = () => {
  return {
    type: TRANSACTION_DIALOG_CLOSE,
    payload: false,
    transactionId: '',
  };
};

export const openStatusDialog = (status, title, content) => {
  return {
    type: STATUS_DIALOG_OPEN,
    payload: true,
    statusInfo: {
      status: status,
      title: title,
      content: content,
    },
  };
};

export const closeStatusDialog = () => {
  return {
    type: STATUS_DIALOG_CLOSE,
    payload: false,
    statusInfo: {},
  };
};
