import tokens from '../config/tokens';

export const LOAD_CURRENCIES = 'Load currencies';
export const CHANGE_CURRENCY = 'Change of default currency';

export function loadCurrencies() {
  return {
    type: LOAD_CURRENCIES,
    payload: tokens,
  }
}

export const changeCurrency = (current, currentAddress) => {
  return {
    type: CHANGE_CURRENCY,
    payload: { current, currentAddress },
  };
};
