import React from 'react';
import {wallets} from '../connectors';
import {useWeb3React} from '@web3-react/core';
import {Button, Grid, Link, makeStyles, Typography} from '@material-ui/core';
import {useHistory} from 'react-router-dom';
import {isMobile} from "react-device-detect";
import Card from "@material-ui/core/Card";
import clsx from 'clsx';

const useStyles = makeStyles(() => ({
    selectBoxActive: {
        '&:hover': {
            filter: 'drop-shadow(0 0 1px rgba(105,143,212,0.5))',
        },
    },
    walletIcon: {
        maxWidth: '30px',
        padding: '5px 5px',
    },
    creditCardIcon: {
        width: '80px',
        height: 'auto',
    },
}));

//TODO: MKO wycignc wspolna logike z Dialogiem(jeli bdzie istnia)
const EmptyDashboard = () => {
    const history = useHistory();
    const classes = useStyles();
    const {activate, connector} = useWeb3React();

    const handleConnection = (wallet) => {
        if (wallet.type === 'META_MASKS_WALLET') {
            if (!window.ethereum || !window.ethereum.isMetaMask) {
                window.open(wallet.url, '_blank');
            } else {
                activate(wallet.provider);
            }
        }
        if (wallet.type === 'ARKANE_WALLET') {
            activate(wallet.provider);
        }
        localStorage.setItem('lastProvider', wallet.type);
    };


    return (
        <Grid container spacing={5} justify="center" wrap="wrap">
            <Grid item xs={12}>
                <Typography component={'h1'} variant={'h3'} className={'p-align-center fcx-my-40'}>FREEZE TOKEN NOW</Typography>
            </Grid>
            <Grid item xs={12} sm={6}>
                <Card className={'w-100 h-100 fcx-px-30 fcx-py-40 fcx-round-10'} elevation={5}>
                    <Typography component={'h2'} variant={'h5'} className={'p-align-center fcx-mb-30'}>FREEZE WITH WALLET</Typography>
                    <Grid container alignItems="center">
                        {wallets.filter(f => (!isMobile || (isMobile && f.mobile))).map((wallet) => {
                            let name = wallet.name;
                            const isCurrent = connector === wallet.provider;
                            const type = wallet.type;
                            if (type === 'META_MASKS_WALLET') {
                                if (!window.ethereum || !window.ethereum.isMetaMask) {
                                    name = 'Install ' + wallet.name;
                                }
                            }
                            return (
                                <Button onClick={() => (isCurrent && activate ? history.push('/new-deposit') : handleConnection(wallet))} variant="outlined" className={'fcx-mb-10'} fullWidth key={wallet.type}>
                                    {isCurrent && <></>} <img className={classes.walletIcon} src={'/wallets/' + wallet.icon} alt={wallet.name}/>
                                    {name}
                                </Button>
                            );
                        })}
                    </Grid>
                </Card>
            </Grid>
            <Grid item xs={12} sm={6}>
                <Card className={clsx('w-100 h-100 fcx-px-30 fcx-py-40 fcx-round-10', classes.selectBoxActive)} elevation={5} style={{cursor: 'pointer'}} onClick={() => history.push('/new-deposit')}>
                    <Typography component={'h2'} variant={'h5'} className={'p-align-center fcx-mb-25'}>FREEZE WITH CREDIT CARD</Typography>
                    <Grid container spacing={2}>
                        <Grid container item justify="center" alignContent="center" alignItems="center" xs={4}>
                            <img className={classes.creditCardIcon} src={'/wallets/icon-credit-card.svg'} alt={'Credit card'}/>
                        </Grid>
                        <Grid item xs={8}>
                            <p>
                                Don`t have a wallet yet?<br/>
                                Create one and make<br/>
                                a deposit using a credit card.
                            </p>
                            <Link href="#" className={"fcx-mt-15"}>Learn more</Link>
                        </Grid>
                    </Grid>
                </Card>
            </Grid>
        </Grid>
    );
};

export default EmptyDashboard;
