import { Box, Grid, makeStyles } from '@material-ui/core';
import moment from 'moment';
import { formatUnits } from '@ethersproject/units';
import React from 'react';
import Tooltip from '@material-ui/core/Tooltip';

const useStyles = makeStyles(() => ({
  lineBorder: {
    borderBottom: '1px solid rgba(105,143,212,0.2)',
    paddingBottom: '2px',
    paddingTop: '5px',
  },
}));

const DepositItemEvents = ({ hdt, isEth, isExpanded, events }) => {
  const classes = useStyles();

  return (<Box pl={1} width="100%" display={isExpanded ? 'block' : 'none'}>
    {events.map((e, i) => {
      const value = formatUnits(e.args.value, isEth ? 18 : 8);
      return (
        <Grid key={i} container item xs={12} className={classes.lineBorder}>
          <Grid item xs={4}>
            <Tooltip title={value + ' ' + events[0].tokenSymbol}><span>{parseFloat(value).toFixed(6)} {events[0].tokenSymbol}</span></Tooltip>
          </Grid>
          <Grid container item xs={4} justify="center">
            {moment.unix(e.timestamp).format('YYYY/MM/DD')}
          </Grid>
          <Grid container item xs={4} justify="flex-end">
            {parseFloat(e.hPrice * value).toFixed(2)} $
          </Grid>
        </Grid>
      );
    })}
    <Grid container item xs={12}>
      <Grid container item xs={12} justify="flex-end">
        {parseFloat(hdt).toFixed(2)} $
      </Grid>
    </Grid>
  </Box>);

};
export default DepositItemEvents;