import { InjectedConnector } from '@web3-react/injected-connector';
import { NetworkConnector } from '@web3-react/network-connector';
import { ArkaneConnector } from './ArkaneConnector';

const NETWORK_URL = process.env.REACT_APP_NETWORK_URL;


export const ALLOWED_CHAIN_ID = [1, 4];

export const NETWORK_CHAIN_ID = parseInt(process.env.REACT_APP_CHAIN_ID ?? '4');

export const injected = new InjectedConnector({
  supportedChainIds: [1, 3, 4, 5, 42],
});

export const network = new NetworkConnector({
  urls: { [NETWORK_CHAIN_ID]: NETWORK_URL },
  defaultChainId: 4,
});

export const arkane = new ArkaneConnector({
  clientId: 'FreezeCapital',
  environment: 'staging',
  windowMode: 'POPUP',
  signUsing: 'POPUP',
  useOverlayWithPopup: true,
  secretType: 'ETHEREUM',
  network: {
    name: 'rinkeby',
    nodeUrl: 'https://rinkeby.infura.io/v3/32ed2b24df8048018d43339b6da96d91',
    chainId: 4,
  },
});

export const wallets = [
  {
    'type': 'META_MASKS_WALLET',
    'name': 'MetaMask',
    'url': 'https://metamask.io',
    'icon': 'metamask.png',
    'provider': injected,
    'mobile': false
  },
  {
    'type': 'ARKANE_WALLET',
    'name': 'Arkane',
    'url': 'https://arkane.network',
    'icon': 'arkane.png',
    'provider': arkane,
    'mobile': true
  },
];

export function findProviderByType(type) {
  const wallet = wallets.filter(w => w.type === type)[0];
  return wallet ? wallet.provider : undefined;
}

export function walletByConnector(connector) {
  return wallets.filter(w => w.provider === connector)[0];
}

export function walletIsMetaMask(connector) {
  const wallet = walletByConnector(connector);
  return wallet && wallet.provider === injected && window.ethereum.isMetaMask;
}

export function currentConnectorName(connector) {
  const wallet = walletByConnector(connector);
  return wallet ? wallet.name : undefined;
}