import { MAX_VALUE, NETWORK_LABELS } from '../constants';
import { formatUnits } from '@ethersproject/units';
import tokens from '../config/tokens';

export function formatAccount(account) {
  return account === null ? '-' : account ? `${account.substring(0, 6)}...${account.substring(account.length - 4)}` : '';
}

export function openAddressOnEtherScan(chainId, address) {
  let url = 'https://';
  if (chainId !== 1) {
    url += NETWORK_LABELS[chainId].toLowerCase() + '.';
  }
  url += 'etherscan.io/address/';
  url += address;
  openInWindow(url);
}

export function openTxOnEtherScan(chainId, address) {
  let url = 'https://';
  if (chainId !== 1) {
    url += NETWORK_LABELS[chainId].toLowerCase() + '.';
  }
  url += 'etherscan.io/tx/';
  url += address;
  openInWindow(url);
}

export function openInWindow(url) {
  const win = window.open(url, '_blank');
  win.focus();
}

export function isMinPriceIsMax(price) {
  return formatUnits(price, 8) === formatUnits(MAX_VALUE, 8);
}

export function tokenAddressToSymbol(chainId, address) {
  return tokens.filter(token => token.address === address)[0].symbol;
}

export function tokenAddressToCoingecoId(address) {
  return tokens.filter(token => token.address === address)[0].coningecoId;
}