import React, { useEffect, useState } from 'react';
import { Box, Grid, Hidden, makeStyles, Typography } from '@material-ui/core';
import StackedChart from './StackedChart';
import DashboardBox from './DashboardBox';
import '../styles/styles.scss';
import { isETH } from '../utils';
import { formatEther, formatUnits } from '@ethersproject/units';
import clsx from 'clsx';
import moment from 'moment';
import { tokenHistory } from '../utils/cachingAxios';
import CircularProgress from '@material-ui/core/CircularProgress';

function dynamicSort(property) {
  let sortOrder = 1;
  if (property[0] === '-') {
    sortOrder = -1;
    property = property.substr(1);
  }
  return function(a, b) {
    /* next line works with strings and numbers,
     * and you may want to customize it to your needs
     */
    const result = (a[property] < b[property]) ? -1 : (a[property] > b[property]) ? 1 : 0;
    return result * sortOrder;
  };
}

const useStyles = makeStyles(() => ({
  red: {
    color: 'red !important',
  },
}));

const DashboardSummary = ({ deposits, priceFetcherContract }) => {
  const classes = useStyles();
  const [activeDeposits, setActiveDeposits] = useState([]);
  const [totalCurrent, setTotalCurrent] = useState(1);
  const [data, setData] = useState([]);
  const [performance, setPerformance] = useState(0);
  const [isLoadAdditionalData, setIsLoadAdditionalData] = useState(true);

  useEffect(async () => {
    const proms = deposits.map(async (d) => {
      const events = d.events;
      const fPrice = priceFetcherContract.currentPrice(events[0].tokenAddress);
      const proms = events
        .filter(e => e.name !== 'Withdraw')
        .map(async (e) => {
          try {
            const r = await tokenHistory(e.coingecoId, moment.unix(e.timestamp).format('DD-MM-YYYY'));
            e.hPrice = r.data.market_data.current_price.usd;
          } catch {
            e.hPrice = 0;
          }
          return e;
        });
      const res = await Promise.all([fPrice, proms]);
      d.cPrice = parseFloat(formatUnits(res[0], 8));
      d.events = await Promise.all(res[1]);
      return d;
    });
    const ad = await Promise.all(proms);
    setActiveDeposits(ad);
    setIsLoadAdditionalData(false);
  }, []);

  useEffect(() => {
    const th = activeDeposits.map(deposit => {
      const { data, events } = deposit;
      const { token } = data;
      return events.filter(e => e.name !== 'Withdraw').map(e => {
        return e.hPrice * formatUnits(e.args.value, isETH(token) ? 18 : 8);
      }).reduce((a, b) => a + b, 0);
    }).reduce((a, b) => a + b, 0);

    const tc = activeDeposits.map(deposit => {
      const { data } = deposit;
      const { token, value } = data;
      return (isETH(token) ? formatEther(value) : formatUnits(value, 8)) * deposit.cPrice;
    }).reduce((a, b) => a + b, 0);
    setTotalCurrent(tc);

    const p = activeDeposits.map(deposit => {
      const { data, events } = deposit;
      const { token, value } = data;
      const val = formatUnits(value, isETH(token) ? 18 : 8) * deposit.cPrice;
      return {
        name: events[0].tokenSymbol,
        value: val,
      };
    }).reduce((x, y) => {
      const l = x.filter(t => t.name == y.name);
      if (l.length == 0) {
        x.push(y);
      } else {
        l[0].value += y.value;
      }
      return x;
    }, []).map(d => {
      d.percentage = (d.value * 100) / tc;
      return d;
    });

    setPerformance(100 - (th / tc * 100));
    const sort = p.sort(dynamicSort('percentage'));
    setData(sort);
  }, [activeDeposits]);

  return (
    <Grid item xs={12}>
      <DashboardBox>
        {isLoadAdditionalData ? <CircularProgress size={20}/> : <Grid container className={'fcx-py-10'}>
          <Grid item xs={8}>
            <Box height="100%" display="flex" flexDirection="column" justifyContent="space-between">
              <Typography variant={'body2'} className={'f-bold'}>PORTFOLIO PERFORMANCE</Typography>
              <StackedChart data={data}/>
            </Box>
          </Grid>
          <Grid container item xs={4} justify="flex-end">
            <Box height="100%" display="flex" flexDirection="column" justifyContent="space-between" alignItems="flex-end">
              <Typography variant={'caption'} className={'fcx-mb-10'}>
                <Hidden xsDown>CURRENT </Hidden>USD VALUE
              </Typography>
              <Typography variant={'h2'}>
                ${parseFloat(totalCurrent).toFixed(2)}
              </Typography>
              <Typography variant={'body1'} className={clsx('f-bold', 'f-green', performance < 0 && classes.red)} align="right">
                {performance > 0 && <>+</>} {parseFloat(performance).toFixed(2)}%
              </Typography>
            </Box>
          </Grid>
        </Grid>}
      </DashboardBox>
    </Grid>
  );
};
export default DashboardSummary;
