import React, { useEffect, useState } from 'react';
import { Box, Collapse, Grid, Hidden, makeStyles, Typography } from '@material-ui/core';
import { isETH } from '../utils';
import { formatEther, formatUnits } from '@ethersproject/units';
import { connect } from 'react-redux';
import moment from 'moment';
import { isMinPriceIsMax } from '../utils/wallet';
import clsx from 'clsx';
import DashboardBox from './DashboardBox';
import CurrencyChart from './CurrencyChart';
import { BigNumber } from 'ethers/utils';
import DepositItemEvents from './DepositItemEvents';
import CircularProgress from '@material-ui/core/CircularProgress';
import { tokenHistory } from '../utils/cachingAxios';
import Tooltip from '@material-ui/core/Tooltip';

const useStyles = makeStyles(() => ({
  main: {
    padding: '5px 5px',
  },
  lineBorder: {
    borderBottom: '1px solid #CEDEE5',
    paddingBottom: '2px',
    paddingTop: '5px',
  },
  span: {
    marginLeft: '5px',
  },
  red: {
    color: 'red !important',
  },
}));


const DepositItem = ({ deposit, currencies }) => {
  const classes = useStyles();

  const { id, events } = deposit;

  const { list: currencyList } = currencies;

  const token = events[0].tokenAddress;
  const isEth = isETH(token);

  const value = events.filter(e => e.name !== 'Withdraw').map(e => e.args.value).reduce((a, b) => a.add(b), new BigNumber(0));

  const unlockTimeUTC = events[0].args.unlockTimeUTC;
  const minPrice = events[0].args.minPrice;

  const [isLoadAdditionalData, setIsLoadAdditionalData] = useState(true);
  const [currentPrice, setCurrentPrice] = useState(1);
  const [eventsWithHistoricalPrice, setEventsWithHistoricalPrice] = useState([]);
  const [historicalDepositValue, setHistoricalDepositValue] = useState(1);

  const newDepositDate = events.filter((el) => el.name === 'NewDeposit')[0].timestamp;
  const endDepositDate = events.filter((el) => el.name === 'Withdraw')[0].timestamp;

  const fValue = isEth ? formatEther(value) : formatUnits(value, 8);

  const cdt = fValue * currentPrice;
  const p = 100 - (historicalDepositValue / cdt * 100);

  const targetDate = moment.unix(unlockTimeUTC.toNumber());
  const targetPrice = formatUnits(minPrice, 8);

  const [isExpanded, setExpanded] = useState(false);

  useEffect(async () => {
    const proms = events
      .filter(e => e.name !== 'Withdraw')
      .map(async (e) => {
        try {
          const r = await tokenHistory(e.coingecoId, moment.unix(e.timestamp).format('DD-MM-YYYY'));
          e.hPrice = r.data.market_data.current_price.usd;
        } catch {
          e.hPrice = 0;
        }
        return e;
      });
    const ewhp = await Promise.all(proms);
    setEventsWithHistoricalPrice(ewhp);
    setCurrentPrice(ewhp[ewhp.length - 1].hPrice);
    setIsLoadAdditionalData(false);
  }, []);

  useEffect(() => {
    const hdv = eventsWithHistoricalPrice.map(e => e.hPrice * formatUnits(e.args.value, isEth ? 18 : 8)).reduce((a, b) => a + b, 0);
    setHistoricalDepositValue(hdv);
  }, [eventsWithHistoricalPrice]);

  return (
    <Grid key={'id-' + id} item xs={12}>
      <DashboardBox p={3} onClick={() => setExpanded(!isExpanded)}>
        <Grid container>
          <Grid container item xs={12}>
            <Grid container item xs={12} sm={5} alignItems="center" className={'fcx-my-5'}>
              <Tooltip title={fValue + ' ' + events[0].tokenSymbol}><Typography variant={'h2'}>
                {parseFloat(fValue).toFixed(6)} {events[0].tokenSymbol}
              </Typography></Tooltip>
            </Grid>
            <Grid container item xs={7} alignItems="center" justify="flex-end">
              {!isMinPriceIsMax(minPrice) && (
                <>
                  <Grid item xs={5}>
                    <Box textAlign="right">
                      TARGET PRICE: <span className={clsx('f-bold', classes.span)}>${parseFloat(targetPrice).toFixed(2)}</span>
                    </Box>
                  </Grid>
                  <Grid container item xs={2} justify="center" alignItems="center">
                    OR
                  </Grid>
                </>
              )}
              <Grid item xs={5}>
                <Box textAlign="right">
                  TARGET DATE: <span className={clsx('f-bold', classes.span)}>{targetDate.format('DD/MM/yyyy')}</span>
                </Box>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Collapse in={isExpanded}>
          <Grid container>
            <Grid container item xs={6} alignItems="center">
              {isLoadAdditionalData ? <CircularProgress size={20}/> : <DepositItemEvents hdt={historicalDepositValue} isEth={isEth} isExpanded={isExpanded} events={eventsWithHistoricalPrice}/>}
            </Grid>
            <Grid item xs={6}>
              <Box width="100%" display="flex" py={1} justifyContent="flex-end" pt={3}>
                <CurrencyChart from={newDepositDate} symbol={currencyList.filter((c) => c.address === token)[0].symbol} to={endDepositDate}/>
              </Box>
            </Grid>
          </Grid>
        </Collapse>
        <Grid container item xs={12}>
          <Grid container item xs={5} justify={'flex-end'} alignItems={'flex-end'}>

          </Grid>
          <Grid container item xs={7} justify="flex-end" alignItems="flex-end">
            <Box pt={2} pb={1}>
              {isLoadAdditionalData ? <CircularProgress size={20}/> : <span>
                withdraw deposit value:
                <span className={clsx('f-bold', classes.span)}>
                  ${parseFloat(cdt).toFixed(2)}
                  <Hidden xsDown>
                    <span className={clsx('f-green', classes.span, p < 0 && classes.red)}>{p > 0 && <>+</>}{parseFloat(p).toFixed(2)}%</span>
                  </Hidden>
                </span>
              </span>}
            </Box>
          </Grid>
        </Grid>
      </DashboardBox>
    </Grid>
  );
};

const mapStateToProps = (state) => ({
  currencies: state.currencies,
});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(DepositItem);
