import React, { useEffect, useState } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { openAccountDialog, openConnectAWalletDialog } from '../actions/dialogActions';
import Button from '@material-ui/core/Button';
import Hidden from '@material-ui/core/Hidden';
import { useWeb3React } from '@web3-react/core';
import { formatEther } from '@ethersproject/units';
import { NETWORK_LABELS } from '../constants';
import { formatAccount } from '../utils/wallet';
import AddressesIdentIcon from './AddressIdentIcon';
import { Grid } from '@material-ui/core';
import LinearProgress from '@material-ui/core/LinearProgress';
import { TRANSACTION_STATUS_PENDING } from '../actions/transactionActions';
import { useTransactionsWatch } from '../hooks/useTransactionsWatch';
import { ALLOWED_CHAIN_ID } from '../connectors';
import Typography from '@material-ui/core/Typography';
import NarrowButton from './NarrowButton';
import { useHistory, useLocation } from 'react-router-dom';

const AccountView = ({ openAccountDialog, transactions, changeOrder }) => {
  const { account, library, chainId } = useWeb3React();
  const [balance, setBalance] = useState(0);
  const [pendingTransactions, setPendingTransactions] = useState(0);
  const history = useHistory();
  const location = useLocation();

  const disableDashboardButton = location.pathname === '/';
  const buttonText = 'DASHBOARD'

  useTransactionsWatch(transactions);

  useEffect(async () => {
    if (!!account && !!library) {
      const balance = await library.getBalance(account);
      setBalance(balance);
    }
  }, [account, library, chainId]);

  useEffect(() => {
    if (transactions) {
      setPendingTransactions(transactions.filter((t) => t.account === account && t.chainId === chainId).filter((t) => t.status === TRANSACTION_STATUS_PENDING).length);
    }
  }, [transactions]);

  return (
    <>
      {changeOrder ? (
        <>
          {chainId !== 1 && (
            <Hidden xsDown>
              <Typography variant={'body2'} className={'f-dark-gray'}>{NETWORK_LABELS[chainId]}</Typography>
            </Hidden>
          )}
          {!disableDashboardButton && <NarrowButton alt={'Dashboard'} color="primary" className={'fcx-mr-5'} onClick={() => history.push('/')}>
            {buttonText}
          </NarrowButton>}
          <Grid item>
            <Button onClick={openAccountDialog} variant="outlined" color="default" size={'small'} className={'fcx-mx-10'} endIcon={<AddressesIdentIcon/>}>
              {formatAccount(account)}
            </Button>
          </Grid>
          <Hidden xsDown>
            {pendingTransactions > 0 ? <PendingTransactions pending={pendingTransactions}/> : <Typography variant={'body2'} className={'f-dark-gray'}>{Number.parseFloat(formatEther(balance)).toFixed(4) + ' ETH'}</Typography>}
          </Hidden>
        </>
      ) : (
        <>

          {!disableDashboardButton && <NarrowButton alt={'Dashboard'} color="primary" className={'fcx-mr-5'} onClick={() => history.push('/')}>
            {buttonText}
          </NarrowButton>}

          {ALLOWED_CHAIN_ID.indexOf(chainId) == -1 && <Typography variant={'body2'} className={'f-red d-inline-block fcx-mr-5'}>Unsupported network</Typography>}

          {chainId !== 1 && (
            <Typography variant={'body2'} className={'f-dark-gray d-inline-block fcx-mr-5'}>{NETWORK_LABELS[chainId]}:</Typography>
          )}

          {pendingTransactions > 0 ? <PendingTransactions pending={pendingTransactions}/> : <Typography variant={'body2'} className={'f-dark-gray d-inline-block fcx-mr-5'}>{Number.parseFloat(formatEther(balance)).toFixed(4) + ' ETH'}</Typography>}

          <Button onClick={openAccountDialog} variant="outlined" color="default" size={'small'} endIcon={<AddressesIdentIcon/>}>
            {formatAccount(account)}
          </Button>
        </>
      )}
    </>
  );
};

const PendingObj = ({ pending }) => {
  return (
    <>
      {pending} trans.
      <LinearProgress color={'secondary'}/>
    </>
  );
};

const PendingTransactions = ({ pending }) => {
  return <Typography variant={'body2'} className={'f-dark-gray d-inline-block fcx-mr-5'}>{<PendingObj pending={pending}/>}</Typography>;
};

const AppBarConnectionInfo = (props) => {
  const { account } = useWeb3React();
  const { openWalletDialog, openAccountDialog, transactions, changeOrder } = props;
  const location = useLocation();
  const history = useHistory();
  
  const disableDashboardButton = location.pathname === '/';

  return (
    <>
      {!account ? (<>
          {!disableDashboardButton && <NarrowButton alt={'Dashboard'} color="primary" className={'fcx-mr-5'} onClick={() => history.push('/')}>
            DASHBOARD
          </NarrowButton>}
        <Button variant="contained" color="primary" disableElevation={true} disableRipple={true} onClick={openWalletDialog}>
          Connect to a wallet
        </Button></>
      ) : (
        <AccountView openAccountDialog={openAccountDialog} changeOrder={changeOrder} transactions={transactions}/>
      )}
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    transactions: state.transactions.transactions,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      openWalletDialog: openConnectAWalletDialog,
      openAccountDialog: openAccountDialog,
    },
    dispatch,
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(AppBarConnectionInfo);
