import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import { DialogTransition } from './common/DialogTransition';
import DialogTitle from './common/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Grid from '@material-ui/core/Grid';
import { bindActionCreators } from 'redux';
import { closeTransactionDialog } from '../actions/dialogActions';
import { connect } from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import { useWeb3React } from '@web3-react/core';
import Button from '@material-ui/core/Button';
import CallMadeIcon from '@material-ui/icons/CallMade';
import { openTxOnEtherScan } from '../utils/wallet';

const TransactionInProgressDialog = (props) => {

  const { isOpenTransactionDialog, transactionId, closeTransactionDialog } = props;
  const { chainId } = useWeb3React();

  return <Dialog
    onClose={closeTransactionDialog}
    TransitionComponent={DialogTransition}
    keepMounted
    maxWidth={'xs'}
    fullWidth={true}
    aria-labelledby="simple-dialog-title"
    open={isOpenTransactionDialog}
  >
    <DialogTitle id="customized-dialog-title" onClose={closeTransactionDialog}>
      Transaction in progress
    </DialogTitle>
    <DialogContent>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <CircularProgress/>
        </Grid>
        <Grid item xs={12}>
          <Button size={'small'} startIcon={<CallMadeIcon/>} onClick={() => openTxOnEtherScan(chainId, transactionId)}>View transaction on Etherscan</Button>
        </Grid>
      </Grid>
    </DialogContent>
  </Dialog>;
};

const mapStateToProps = (state) => {
  return state.dialog;
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    closeTransactionDialog: closeTransactionDialog,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(TransactionInProgressDialog);