import React, { useEffect, useState } from 'react';
import { AppBar, Button, Drawer, Grid, Hidden, IconButton, Link as MaterialLink, makeStyles, MenuItem, Toolbar, Typography } from '@material-ui/core';
import { Menu, TrendingUpOutlined } from '@material-ui/icons';
import AppBarConnectionInfo from '../AppBarConnectionInfo';
import { Logo } from './SVGs';


const WEB_HOST = process.env.REACT_APP_WEB_HOST;

const headersData = [
  // {
  //   label: 'Team',
  //   href: WEB_HOST + '/team',
  // },
  // {
  //   label: 'FAQ',
  //   href: WEB_HOST + '/faq',
  // },
  // {
  //   label: 'Contact',
  //   href: WEB_HOST + '/contact',
  // },
];

const useStyles = makeStyles(() => ({
  header: {
    backgroundColor: '#ffffff',
  },
  toolbarDesktop: {
    display: 'flex',
    justifyContent: 'flex-start',
  },
  toolbarMobile: {
    display: 'flex',
    justifyContent: 'space-between',
  },
  drawerContainer: {
    padding: '7% 5%',
    width: '300px',
  },
  appBarBottom: {
    top: 'auto',
    bottom: 0,
    background: '#fff',
  },
}));

export default function Header() {
  const classes = useStyles();

  const [state, setState] = useState({
    mobileView: false,
    drawerOpen: false,
  });

  const { mobileView, drawerOpen } = state;

  useEffect(() => {
    const setResponsiveness = () => {
      return window.innerWidth < 1064 ? setState((prevState) => ({ ...prevState, mobileView: true })) : setState((prevState) => ({ ...prevState, mobileView: false }));
    };

    setResponsiveness();

    window.addEventListener('resize', () => setResponsiveness());
  }, []);

  const displayDesktop = () => {
    return (
      <>
        <Toolbar className={classes.toolbarDesktop}>
          <Grid container justify="space-between" alignItems="center" wrap="nowrap">
            <Grid item xs={4}>
              {cfLogo}
            </Grid>
            <Grid item xs={4} className='p-align-center'>
              {getMenuButtons()}
            </Grid>
            <Grid item xs={4} className={'p-align-right'}>
              <AppBarConnectionInfo/>
            </Grid>
          </Grid>
        </Toolbar>

        <Hidden xsUp>
          <AppBar position="fixed" className={classes.appBarBottom} elevation={5}>
            <Toolbar>
              <Grid container justify="center" alignItems="center">
                <Typography variant="body2" className={'f-dark-gray'}>
                  Copyrights &copy; 2021 <TrendingUpOutlined fontSize={'small'} className={'fcx-mx-10 f-blue fcx-footer-icon'}/> FreezeCapital<br/>
                </Typography>
              </Grid>
            </Toolbar>
          </AppBar>
        </Hidden>
      </>
    );
  };

  const displayMobile = () => {
    const handleDrawerOpen = () => setState((prevState) => ({ ...prevState, drawerOpen: true }));
    const handleDrawerClose = () => setState((prevState) => ({ ...prevState, drawerOpen: false }));

    return (
      <>
        <Toolbar className={classes.toolbarMobile}>
          <Drawer
            {...{
              anchor: 'right',
              open: drawerOpen,
              onClose: handleDrawerClose,
            }}
          >
            <div className={classes.drawerContainer}>{getDrawerChoices()}</div>
          </Drawer>
          <div>{cfLogo}</div>
          <IconButton
            {...{
              edge: 'start',
              color: 'primary',
              'aria-label': 'menu',
              'aria-haspopup': 'true',
              onClick: handleDrawerOpen,
            }}
          >
            <Menu/>
          </IconButton>
        </Toolbar>
        <AppBar position="fixed" className={classes.appBarBottom}>
          <Toolbar>
            <Grid container spacing={5} justify="center" wrap="nowrap" alignItems="center">
              <AppBarConnectionInfo changeOrder/>
            </Grid>
          </Toolbar>
        </AppBar>
      </>
    );
  };

  const getDrawerChoices = () => {
    return headersData.map(({ label, href }) => {
      return (
        <MaterialLink
          {...{
            component: MaterialLink,
            href: href,
            color: 'primary',
            style: { textDecoration: 'none' },
            key: label,
          }}
        >
          <MenuItem>{label}</MenuItem>
        </MaterialLink>
      );
    });
  };

  const cfLogo = (
    <a href={WEB_HOST} alt="Freeze Capital" className={'fcx-header-logo'}>
      <Logo width="200px"/>
    </a>
  );

  const getMenuButtons = () => {
    return headersData.map(({ label, href }) => {
      return (
        <Button
          {...{
            key: label,
            color: 'primary',
            variant: 'text',
            href: href,
            component: MaterialLink,
            className: 'fcx-mr-10 f-black fcx-no-underline',
          }}
        >
          {label}
        </Button>
      );
    });
  };

  return (
    <header>
      <AppBar elevation={2} className='b-white'>
        {mobileView ? displayMobile() : displayDesktop()}
      </AppBar>
    </header>
  );
}
