import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {closeAccountDialog, openConnectAWalletDialog} from '../actions/dialogActions';
import Grid from '@material-ui/core/Grid';
import {useWeb3React} from '@web3-react/core';
import {formatAccount, openAddressOnEtherScan, openTxOnEtherScan} from '../utils/wallet';
import CallMadeIcon from '@material-ui/icons/CallMade';
import CopyAddressButton from './CopyAddressButton';
import AddressesIdentIcon from './AddressIdentIcon';
import {currentConnectorName} from '../connectors';
import DialogTitle from './common/DialogTitle';
import {DialogTransition} from './common/DialogTransition';

const HistoryTransaction = ({account, chainId, transactions}) => {
    return <Grid container>
        {
            transactions
                .filter(t => t.account === account && t.chainId === chainId)
                .reverse()
                .map((t, i) => {
                    return <Grid key={i} item xs={12}>{t.action} {t.value} {t.token} {t.status} {t.result} <Button className={'f-dark-gray f-normal'} size={'small'} startIcon={<CallMadeIcon/>} onClick={() => openTxOnEtherScan(chainId, t.tx)}>View transaction on Etherscan</Button></Grid>;
                })
        }
    </Grid>;
};

const AccountDialog = (props) => {

    const {chainId, account, connector} = useWeb3React();
    const {dialog, closeAccountDialog, openConnectAWalletDialog, transactionsHistory} = props;
    const {isOpenAccountDialog} = dialog;

    const handleChange = () => {
        closeAccountDialog();
        openConnectAWalletDialog();
    };

    return (
        <Dialog
            onClose={closeAccountDialog}
            TransitionComponent={DialogTransition}
            keepMounted
            maxWidth={'xs'}
            aria-labelledby="account-dialog"
            open={isOpenAccountDialog}
        >
            <DialogTitle id="account-dialog" onClose={closeAccountDialog}>
                Account
            </DialogTitle>
            <DialogContent>
                <Grid container>
                    <Grid item xs={10}>
                      <Typography variant={'body2'}>Connected with {currentConnectorName(connector)}</Typography>
                    </Grid>
                    <Grid item xs={2}>
                        <Button size={'small'} onClick={handleChange}>Change</Button>
                    </Grid>
                    <Grid item xs={12} className={'fcx-mb-5'}>
                        <AddressesIdentIcon/><Typography variant={'h5'} className={'d-inline-block'}>{formatAccount(account)}</Typography>
                    </Grid>
                    <Grid item xs={6} className={'fcx-mb-10'}><CopyAddressButton address={account}/></Grid>
                    <Grid item xs={6} className={'fcx-mb-10'}><Button className={'f-dark-gray f-normal'} size={'small'} startIcon={<CallMadeIcon/>} onClick={() => openAddressOnEtherScan(chainId, account)}>View on Etherscan</Button></Grid>
                    {transactionsHistory.transactions.filter(t => t.account === account && t.chainId === chainId).length > 0 && <Grid item xs={12}><HistoryTransaction chainId={chainId} account={account} transactions={transactionsHistory.transactions}/></Grid>}
                </Grid>
            </DialogContent>
        </Dialog>
    );
};

const mapStateToProps = (state) => {
    return {
        dialog: state.dialog,
        transactionsHistory: state.transactions,
    };
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        closeAccountDialog: closeAccountDialog,
        openConnectAWalletDialog: openConnectAWalletDialog,
    }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(AccountDialog);


