import React from 'react';
import { Box, Container, Grid, Typography } from '@material-ui/core';
import DashboardSummary from './DashboardSummary';
import DepositItem from './DepositItem';
import { useHistory } from 'react-router-dom';
import { isActiveDeposit, useDeposits, withdraw } from '../deposit';
import CircularProgress from '@material-ui/core/CircularProgress';
import { useWeb3React } from '@web3-react/core';
import EmptyDashboard from './DashboardEmpty';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import NarrowButton from './NarrowButton';
import { getProviderOrSigner } from '../utils';
import DepositHistoryItem from './DepositHistoryItem';
import { Contract } from '@ethersproject/contracts';
import { PRICE_FETCHER_ABI_ADDRESS, PRICE_FETCHER_ABI_INTERFACE } from '../constants';

const FullDashboard = ({ deposits }) => {
  const history = useHistory();
  const { account, library, chainId } = useWeb3React();

  const handleNewDeposit = () => {
    history.push('/new-deposit');
  };

  const handleWithdraw = (id) => {
    withdraw(account, library, chainId, id);
  };

  const historicalDeposits = deposits.filter((item) => !isActiveDeposit(item.data));

  const priceFetcherContract = new Contract(PRICE_FETCHER_ABI_ADDRESS[chainId], PRICE_FETCHER_ABI_INTERFACE, getProviderOrSigner(library, account));

  return (
    <>
      <Grid container className={'fcx-mt-10 fcx-mb-15'}>
        <Grid item xs={6}>
          <Typography variant="h3">
            DASHBOARD
          </Typography>
        </Grid>
        <Grid container item xs={6} justify="flex-end">
          <Box display="flex" alignItems="center">
            <NarrowButton color="primary" onClick={handleNewDeposit}>
              NEW DEPOSIT
            </NarrowButton>
          </Box>
        </Grid>
      </Grid>
      <Box>
        <Grid container spacing={2}>
          <DashboardSummary deposits={deposits.filter((item) => isActiveDeposit(item.data))} priceFetcherContract={priceFetcherContract}/>
          <Grid item xs={12}>
            <Typography variant="body2" className={'fcx-mt-20 fcx-mb-5 f-bold'}>
              ACTIVE DEPOSITS
            </Typography>
          </Grid>
          {deposits
            .filter((item) => isActiveDeposit(item.data))
            .map((item) => {
              return <DepositItem key={item.id} deposit={item} handleWithdraw={handleWithdraw} ethProvider={getProviderOrSigner(library, account)} priceFetcherContract={priceFetcherContract}/>;
            })}
          {historicalDeposits.length > 0 && <><Grid item xs={12}>
            <Typography variant="body2" className={'fcx-mt-20 fcx-mb-5 f-bold'}>
              HISTORY
            </Typography>
          </Grid>
            {historicalDeposits.map((item) => {
              return <DepositHistoryItem key={item.id} deposit={item} handleWithdraw={handleWithdraw} disabled/>;
            })}
          </>}
        </Grid>
      </Box>
    </>
  );
};

const Dashboard = ({ transactions }) => {

  const { deposits, isLoading } = useDeposits(transactions);
 
  return (
    <Container maxWidth="md">
      {isLoading && (
        <Box mt={5} textAlign="center">
          <CircularProgress/>
        </Box>
      )}
      {!isLoading && (deposits.length === 0 ? <EmptyDashboard/> : <FullDashboard deposits={deposits}/>)}
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    transactions: state.transactions.transactions,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);
