import { useEffect, useState } from 'react';
import { useWeb3React } from '@web3-react/core';
import { TRANSACTION_RESULT_ERROR, TRANSACTION_RESULT_SUCCESS, TRANSACTION_STATUS_PENDING, transactionProcessed } from '../actions/transactionActions';
import store from '../store';
import { closeTransactionDialog } from '../actions/dialogActions';

export function useTransactionsWatch(transactions) {

  const [refreshInterval, setRefreshInterval] = useState(0);

  const { account, chainId, library, active } = useWeb3React();

  const fetchData = () => {
    if (active) {
      transactions.filter(t => t.account === account && t.chainId === chainId && t.status === TRANSACTION_STATUS_PENDING).forEach(t => {
        library.getTransactionReceipt(t.tx).then(res => {
          if (res && res.blockNumber && res.blockNumber != null) {
            store.dispatch(transactionProcessed(t.tx, res.status === 1 ? TRANSACTION_RESULT_SUCCESS : TRANSACTION_RESULT_ERROR));
            store.dispatch(closeTransactionDialog());
          }
        });
      });
    }
  };

  useEffect(() => {
    if (transactions) {
      const isPending = transactions.filter(t => t.account === account && t.chainId === chainId && t.status === TRANSACTION_STATUS_PENDING).length > 0;
      setRefreshInterval(active ? isPending ? 2000 : 0 : 0);
    }
  }, [transactions]);

  useEffect(() => {
    if (active && refreshInterval && refreshInterval > 0) {
      fetchData();
      const interval = setInterval(fetchData, refreshInterval);
      return () => clearInterval(interval);
    }
  }, [transactions, refreshInterval, chainId, account, active]);
}