import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { closeConnectAWalletDialog } from '../actions/dialogActions';
import { useWeb3React } from '@web3-react/core';
import { wallets } from '../connectors';
import DialogTitle from './common/DialogTitle';
import { DialogTransition } from './common/DialogTransition';
import {isMobile} from 'react-device-detect';

const useStyles = makeStyles(() => ({
  connectButton: {
    marginTop: '10px',
    marginBottom: '10px',
    padding: '10px 10px',
    fontWeight: 'bold',
    minWidth: '80%',
  },
  walletIcon: {
    maxWidth: '30px',
    padding: '5px 5px',
  },
}));

const ConnectWalletDialog = (props) => {
  const { activate, connector } = useWeb3React();

  const classes = useStyles();
  const { isOpenConnectAWalletDialog, closeConnectAWalletDialog } = props;

  const handleConnection = (wallet) => {
    if (wallet.type === 'META_MASKS_WALLET') {
      if (!window.ethereum || !window.ethereum.isMetaMask) {
        window.open(wallet.url, '_blank');
      } else {
        activate(wallet.provider);
      }
    }
    if (wallet.type === 'ARKANE_WALLET') {
      activate(wallet.provider);
    }
    localStorage.setItem('lastProvider', wallet.type);
    closeConnectAWalletDialog();
  };

  return (
    <Dialog onClose={closeConnectAWalletDialog} TransitionComponent={DialogTransition} keepMounted aria-labelledby="simple-dialog-title" open={isOpenConnectAWalletDialog} maxWidth={'xs'} fullWidth={true}>
      <DialogTitle id="customized-dialog-title" onClose={closeConnectAWalletDialog}>
        Connect to a wallet
      </DialogTitle>
      <DialogContent>
        {wallets.filter(f => (!isMobile || (isMobile && f.mobile))).map((wallet) => {
          let name = wallet.name;
          const isCurrent = connector === wallet.provider;
          const type = wallet.type;
          if (type === 'META_MASKS_WALLET') {
            if (!window.ethereum || !window.ethereum.isMetaMask) {
              name = 'Install ' + wallet.name;
            }
          }
          return (
            <Button onClick={() => handleConnection(wallet)} variant="outlined" className={classes.connectButton} fullWidth key={wallet.type}>
              {isCurrent && <></>} <img className={classes.walletIcon} src={'/wallets/' + wallet.icon} alt={wallet.name}/>
              {name}
            </Button>
          );
        })}
      </DialogContent>
    </Dialog>
  );
};

const mapStateToProps = (state) => {
  return state.dialog;
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      closeConnectAWalletDialog: closeConnectAWalletDialog,
    },
    dispatch,
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(ConnectWalletDialog);
