import { ACCOUNT_DIALOG_CLOSE, ACCOUNT_DIALOG_OPEN, CONNECT_A_WALLET_DIALOG_CLOSE, CONNECT_A_WALLET_DIALOG_OPEN, CURRENCY_DIALOG_CLOSE, CURRENCY_DIALOG_OPEN, STATUS_DIALOG_CLOSE, STATUS_DIALOG_OPEN, TRANSACTION_DIALOG_CLOSE, TRANSACTION_DIALOG_OPEN } from '../actions/dialogActions';

const initState = {
  isOpenConnectAWalletDialog: false,
  isOpenAccountDialog: false,
  isOpenCurrencyDialog: false,
  isOpenTransactionDialog: false,
  isOpenStatusDialog: false,
  transactionId: '',
  statusInfo: {},
};

const dialogReducer = (state = initState, action) => {
  switch (action.type) {
    case CONNECT_A_WALLET_DIALOG_OPEN: {
      return { ...state, isOpenConnectAWalletDialog: action.payload };
    }
    case CONNECT_A_WALLET_DIALOG_CLOSE: {
      return { ...state, isOpenConnectAWalletDialog: action.payload };
    }
    case ACCOUNT_DIALOG_OPEN: {
      return { ...state, isOpenAccountDialog: action.payload };
    }
    case ACCOUNT_DIALOG_CLOSE: {
      return { ...state, isOpenAccountDialog: action.payload };
    }
    case CURRENCY_DIALOG_OPEN: {
      return { ...state, isOpenCurrencyDialog: action.payload };
    }
    case CURRENCY_DIALOG_CLOSE: {
      return { ...state, isOpenCurrencyDialog: action.payload };
    }
    case TRANSACTION_DIALOG_OPEN: {
      return { ...state, isOpenTransactionDialog: action.payload, transactionId: action.transactionId };
    }
    case TRANSACTION_DIALOG_CLOSE: {
      return { ...state, isOpenTransactionDialog: action.payload };
    }
    case STATUS_DIALOG_OPEN: {
      return { ...state, isOpenStatusDialog: action.payload, statusInfo: action.statusInfo };
    }
    case STATUS_DIALOG_CLOSE: {
      return { ...state, isOpenStatusDialog: action.payload, statusInfo: action.statusInfo };
    }
  }
  return state;
};

export default dialogReducer;