import { useEffect, useState } from 'react';
import { DATA_REFRESH } from '../constants';
import { useActiveWeb3React } from './index';

export function useGasPrice() {

  const { chainId, connector, account, library } = useActiveWeb3React();

  const [gas, setGas] = useState(0.0);
  const [refreshInterval] = useState(DATA_REFRESH);

  const fetchData = () => {
    library.send('eth_gasPrice', []).then(data => {
      setGas(data);
    });
  };

  useEffect(() => {
    if (refreshInterval && refreshInterval > 0) {
      fetchData();
      const interval = setInterval(fetchData, refreshInterval);
      return () => clearInterval(interval);
    }
  }, [refreshInterval, chainId, connector, account, library]);

  return { gas };
}