export const NEW_TRANSACTION = 'Register new transaction';
export const TRANSACTION_PROCESSED = 'Transaction processed';

export const TRANSACTION_STATUS_PENDING = 'PENDING';
export const TRANSACTION_STATUS_DONE = 'DONE';

export const TRANSACTION_RESULT_UNKNOWN = 'UNKNOWN';
export const TRANSACTION_RESULT_SUCCESS = 'SUCCESS';
export const TRANSACTION_RESULT_ERROR = 'ERROR';

export const TRANSACTION_TYPE_NEW = 'New Deposit';
export const TRANSACTION_TYPE_EXPAND = 'Expand Deposit';
export const TRANSACTION_TYPE_WITHDRAWAL = 'Withdrawal Deposit';

export const registerNewTransaction = (account, chainId, action, tx, value, tokenSymbol) => {
  return {
    type: NEW_TRANSACTION,
    payload: {
      account: account,
      chainId: chainId,
      tx: tx,
      action: action,
      value: value,
      token: tokenSymbol,
      status: TRANSACTION_STATUS_PENDING,
      result: TRANSACTION_RESULT_UNKNOWN,
    },
  };
};

export const transactionProcessed = (tx, result) => {
  return {
    type: TRANSACTION_PROCESSED,
    payload: {
      tx: tx,
      status: TRANSACTION_STATUS_DONE,
      result: result,
    },
  };
};
