import { useEffect, useState } from 'react';
import axios from 'axios';

const isSuccess = (res) => {
  return res.status === 200;
};

export function useRampInfo(token) {

  const initialState = { isSupported: true };
  const [info, setInfo] = useState(initialState);

  useEffect(async () => {
    let is = initialState;
    const res = await axios.get('https://api-instant.ramp.network/api/host-api/assets');
    if (isSuccess(res)) {
      const data = res.data;
      const { assets } = data;
      const tokenData = assets.filter(a => a.symbol === token && a.hidden === false).map(ad => {
        const { decimals, maxFeePercent, minPurchaseCryptoAmount, symbol, maxSinglePurchase, networkFeeEur } = ad;
        return {
          symbol: symbol,
          minPurchaseCryptoAmount: minPurchaseCryptoAmount,
          maxSinglePurchase: maxSinglePurchase.USD,
          maxFeePercent: maxFeePercent.USD,
          decimals: decimals,
          networkFeeEur: networkFeeEur,
          isSupported: true,
        };
      });
      if (tokenData.length > 0) {
        is = tokenData[0];
      } else {
        is = { ...is, isSupported: false };
      }
    }
    setInfo(is);
  }, [token]);
  return info;
}