import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Line } from 'peity-react';

const TARGET_CURRENCY = 'usd'; //(usd, eur, jpy, etc.)
const ETHEREUM = { symbol: 'ETH', name: 'ethereum' };
const WBTC = { symbol: 'WBTC', name: 'wrapped-bitcoin' };

const getURL = (symbol, from, to) => {
  let name = ETHEREUM.name;
  if (symbol === WBTC.symbol) {
    name = WBTC.name;
  }
  return 'https://api.coingecko.com/api/v3/coins/' + name + '/market_chart/range?vs_currency=' + TARGET_CURRENCY + '&from=' + from + '&to=' + to;
};

const CurrencyChart = ({ symbol, from, to = Date.now() }) => {
  const [points, setPoints] = useState([]);
  const [show, setShow] = useState(false);
  const url = getURL(symbol, from, to);
  useEffect(() => {
    axios
      .get(url)
      .then(function (r) {
        setPoints(r.data.prices.map((e) => e[1]));
        setShow(true);
      })
      .catch(function (error) {
        console.log(error);
      });

    return () => {
      setPoints([]);
      setShow(false);
    };
  }, []);

  return show ? <Line values={points} height={100} width={200} strokeColor={'#30B66B'} fillColor={'#ffffff'}/> : <></>; //stroke="#30B66B" fill="#30B66B"
};

export default CurrencyChart;
