import { useEffect, useState } from 'react';
import { useWeb3React } from '@web3-react/core';
import { CONTRACT_ABI_INTERFACE, CONTRACT_ADDRESS, ERC20_ABI_INTERFACE, ETHEREUM_ADDRESS, MAX_VALUE } from '../constants';
import { getProviderOrSigner, isETH } from '../utils';
import { Contract } from '@ethersproject/contracts';
import { formatEther, formatUnits, parseEther, parseUnits } from '@ethersproject/units';
import { openStatusDialog, openTransactionDialog, STATUS_ERROR } from '../actions/dialogActions';

import store from '../store';
import { registerNewTransaction, TRANSACTION_TYPE_EXPAND, TRANSACTION_TYPE_NEW, TRANSACTION_TYPE_WITHDRAWAL } from '../actions/transactionActions';
import { ALLOWED_CHAIN_ID } from '../connectors';
import { tokenAddressToCoingecoId, tokenAddressToSymbol } from '../utils/wallet';

export const isActiveDeposit = (deposit) => {
  const { token, value } = deposit;
  const isEth = isETH(token);
  const val = formatUnits(value, isEth ? 18 : 8);
  return parseFloat(val) > 0;
};

export const findDepositByIndex = async (library, account, chainId, index) => {
  const contract = new Contract(CONTRACT_ADDRESS[chainId], CONTRACT_ABI_INTERFACE, getProviderOrSigner(library, account));
  const data = await contract.deposits(account, index);
  return data;
};

const supportedEvents = {
  NewDeposit: 5,
  Withdraw: 3,
  AddToDeposit: 1,
};

const getTokenIdByEvent = (c, e) => {
  switch (e.name) {
    case 'NewDeposit':
      return e.args.token;
    case 'AddToDeposit':
      return c[e.args.depositIndex];
    case 'Withdraw':
      return c[e.args[3]];
  }
  return ETHEREUM_ADDRESS;
};

