export function useDeposits(transactions) {

  const { active, chainId, connector, account, library } = useWeb3React();
  const [deposits, setDeposits] = useState([]);
  const [isLoading, setLoading] = useState(false);

  const depositsTokens = [];

  const enhancementSync = (e) => {
    if (e.name === 'NewDeposit') {
      depositsTokens[e.args.index.toNumber()] = e.args.token;
    }
    const tokenAddress = getTokenIdByEvent(depositsTokens, e);
    e.tokenAddress = tokenAddress;
    e.tokenSymbol = tokenAddressToSymbol(chainId, tokenAddress);
    e.coingecoId = tokenAddressToCoingecoId(tokenAddress);
    return e;
  };

  const enhancementAsync = async (e) => {
    const r = await library.getBlock(e.blockNumber);
    e.timestamp = r.timestamp;
    return e;
  };

  useEffect(async () => {
    if (active && ALLOWED_CHAIN_ID.indexOf(chainId) != -1) {

      const cryptoFreezerContract = new Contract(CONTRACT_ADDRESS[chainId], CONTRACT_ABI_INTERFACE, getProviderOrSigner(library, account));

      console.time('useDeposits');
      setLoading(true);
      console.time('events');
      const logs = await library.getLogs({
        address: CONTRACT_ADDRESS[chainId],
        fromBlock: 0, toBlock: 'latest',
      });

      const logsPromises = logs
        .map(log => {
          const l = CONTRACT_ABI_INTERFACE.parseLog(log);
          l.blockNumber = log.blockNumber;//neeed block number
          return l;
        })
        .filter(l => l.args.owner === account)
        .filter(l => Object.keys(supportedEvents).indexOf(l.name) >= 0)
        .map(l => enhancementSync(l))
        .map(l => enhancementAsync(l));

      const resolvedLogs = await Promise.all(logsPromises);
      console.timeEnd('events');

      const filteredEvents = resolvedLogs.reduce((acc, k) => {
        const index = k.args[supportedEvents[k.name]];
        acc[index] = acc[index] || [];
        acc[index].push(k);
        return acc;
      }, {});


      console.time('nextDepositIndex');
      const lastIndex = await cryptoFreezerContract.nextDepositIndex(account);
      console.timeEnd('nextDepositIndex');

      const depositPromises = [];

      const resolveDepositByIndex = (contract, filteredEvents, index) => {
        return new Promise((resolve, reject) => {
          Promise.all([contract.deposits(account, index), contract.isUnlocked(account, index)]).then(res => {
            resolve({ id: index, data: res[0], isUnlocked: res[1], events: filteredEvents[index] });
          }).catch(() => {
            reject({ id: index });
          });
        });
      };
      console.time('deposits');
      for (let i = lastIndex.toNumber() - 1; i >= 0; i--) {
        depositPromises.push(resolveDepositByIndex(cryptoFreezerContract, filteredEvents, i));
      }
      const depositsResults = await Promise.all(depositPromises);
      console.timeEnd('deposits');
      setDeposits(depositsResults);
      setLoading(false);
      console.timeEnd('useDeposits');
    } else {
      setDeposits([]);
      setLoading(false);
    }
  }, [active, chainId, connector, account, transactions]);
  return { deposits, isLoading };
}

export async function makeDeposit(tokenSymbol, tokenAddress, value, unlockTime, isMinPriceSet, minPriceFloat, account, library, chainId) {
  const contract = new Contract(CONTRACT_ADDRESS[chainId], CONTRACT_ABI_INTERFACE, getProviderOrSigner(library, account));
  const minPrice = isMinPriceSet ? parseUnits('' + minPriceFloat, 8) : MAX_VALUE;
  if (isETH(tokenAddress)) {
    const res = await contract.depositETH(unlockTime.unix(), minPrice, { value: parseEther('' + value) });
    store.dispatch(openTransactionDialog(res.hash));
    store.dispatch(registerNewTransaction(account, chainId, TRANSACTION_TYPE_NEW, res.hash, value, tokenSymbol));
    return Promise.resolve(res.hash);
  } else {
    const contractToken = new Contract(tokenAddress, ERC20_ABI_INTERFACE, getProviderOrSigner(library, account));
    const isSupported = await contract.isTokenSupported(tokenAddress);
    if (isSupported) {
      try {
        const allowance = await contractToken.allowance(account, CONTRACT_ADDRESS[chainId]);
        if (!allowance) {
          await contractToken.approve(CONTRACT_ADDRESS[chainId], MAX_VALUE);
        }
        const deposit = await contract.depositERC20(tokenAddress, parseUnits('' + value, 8), unlockTime.unix(), minPrice);
        store.dispatch(openTransactionDialog(deposit.hash));
        store.dispatch(registerNewTransaction(account, chainId, TRANSACTION_TYPE_NEW, deposit.hash, value, tokenSymbol));
        return Promise.resolve(deposit.hash);
      } catch (e) {
        store.dispatch(openStatusDialog(STATUS_ERROR, 'Error', e.message));
        return Promise.reject(e.message);
      }
    } else {
      store.dispatch(openStatusDialog(STATUS_ERROR, 'Error', 'Token isn\'t supported'));
      return Promise.reject('Token isn\'t supported');
    }
  }
}

export async function estimateGasDeposit(tokenSymbol, tokenAddress, value, unlockTime, isMinPriceSet, minPriceFloat, account, library, chainId) {
  try {
    if (value > 0) {
      const contract = new Contract(CONTRACT_ADDRESS[chainId], CONTRACT_ABI_INTERFACE, getProviderOrSigner(library, account));
      const minPrice = isMinPriceSet ? parseUnits('' + minPriceFloat, 8) : MAX_VALUE;
      if (isETH(tokenAddress)) {
        const res = await contract.estimateGas.depositETH(unlockTime.unix(), minPrice, { value: parseEther('' + value) });
        return Promise.resolve(res);
      } else {
        const isSupported = await contract.isTokenSupported(tokenAddress);
        if (isSupported) {
          const res = await contract.estimateGas.depositERC20(tokenAddress, parseUnits('' + value, 8), unlockTime.unix(), minPrice);
          return Promise.resolve(res);
        } else {
          return Promise.reject('-1');
        }
      }
    } else {
      return Promise.reject('-1');
    }
  } catch {
    return Promise.reject('-1');
  }
}

export async function withdraw(account, library, chainId, depositIndex) {
  const contract = new Contract(CONTRACT_ADDRESS[chainId], CONTRACT_ABI_INTERFACE, getProviderOrSigner(library, account));
  const isUnlocked = await contract.isUnlocked(account, depositIndex);
  if (isUnlocked) {
    const data = await contract.deposits(account, depositIndex);
    const { token, value } = data;
    const isEth = isETH(token);
    const currentValue = isEth ? formatEther(value) : formatUnits(value, 8);
    if (parseFloat(currentValue) > 0) {
      const res = isEth ? await contract.withdrawETH(account, depositIndex) : await contract.withdrawERC20(account, depositIndex);
      store.dispatch(openTransactionDialog(res.hash));
      store.dispatch(registerNewTransaction(account, chainId, TRANSACTION_TYPE_WITHDRAWAL, res.hash, depositIndex, ''));
      return Promise.resolve(res.hash);
    } else {
      //error was withdrawed
      store.dispatch(openStatusDialog(STATUS_ERROR, 'Error', 'Deposit was already withdrawn'));
      return Promise.reject('Deposit was already withdrawn');
    }
  } else {
    // error is not unlocked
    store.dispatch(openStatusDialog(STATUS_ERROR, 'Error', 'Deposit isn\'t unlocked anymore'));
    return Promise.reject('Deposit isn\'t unlocked anymore');
  }
}

export async function expandDeposit(account, library, chainId, tokenAddress, tokenSymbol, depositIndex, value) {
  try {
    const contract = new Contract(CONTRACT_ADDRESS[chainId], CONTRACT_ABI_INTERFACE, getProviderOrSigner(library, account));
    if (isETH(tokenAddress)) {
      const res = await contract.addToDepositETH(depositIndex, { value: parseEther('' + value) });
      store.dispatch(openTransactionDialog(res.hash));
      store.dispatch(registerNewTransaction(account, chainId, TRANSACTION_TYPE_EXPAND, res.hash, value, tokenSymbol));
      return Promise.resolve(res.hash);
    } else {
      const res = await contract.addToDepositERC20(depositIndex, parseUnits('' + value, 8));
      store.dispatch(openTransactionDialog(res.hash));
      store.dispatch(registerNewTransaction(account, chainId, TRANSACTION_TYPE_EXPAND, res.hash, value, tokenSymbol));
      return Promise.resolve(res.hash);
    }
  } catch (e) {
    store.dispatch(openStatusDialog(STATUS_ERROR, 'Error', e.message));
    return Promise.reject(e.message);
  }
}

export async function estimateGasExpandDeposit(account, library, chainId, tokenAddress, tokenSymbol, depositIndex, value) {
  try {
    const contract = new Contract(CONTRACT_ADDRESS[chainId], CONTRACT_ABI_INTERFACE, getProviderOrSigner(library, account));
    if (isETH(tokenAddress)) {
      const res = await contract.estimateGas.addToDepositETH(depositIndex, { value: parseEther('' + value) });
      return Promise.resolve(res);
    } else {
      const res = await contract.estimateGas.addToDepositERC20(depositIndex, parseUnits('' + value, 8));
      return Promise.resolve(res);
    }
  } catch {
    return Promise.reject(-1);
  }
}
