import React, {useState} from 'react';
import {bindActionCreators} from 'redux';
import {closeCurrencyDialog} from '../actions/dialogActions';
import Dialog from '@material-ui/core/Dialog';
import {DialogTransition} from './common/DialogTransition';
import DialogTitle from './common/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import {connect} from 'react-redux';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Typography from '@material-ui/core/Typography';
import {makeStyles} from '@material-ui/core';
import {changeCurrency} from '../actions/currencyActions';
import MenuList from '@material-ui/core/MenuList';
import _ from 'lodash';

const useStyles = makeStyles(() => ({
    tokenIcon: {
        maxWidth: '30px',
        margin: '5px 5px',
    },
    list: {
        overflow: 'auto',
        minHeight: '200px',
        maxHeight: '500px',
    },
}));

const CoinChooseDialog = ({balance = [], dialog, currencies, closeCurrencyDialog, changeCurrency}) => {
    const classes = useStyles();

    const {isOpenCurrencyDialog} = dialog;
    const [listOfTokens, setListOfTokens] = useState(currencies.list || []);

    const handleSubmit = (code, address) => {
        changeCurrency(code, address);
        closeCurrencyDialog();
    };

    const handleChange = (e) => {
        const value = e.target.value.toUpperCase();
        setListOfTokens(_.cloneDeep(currencies.list).filter(t => (t.name.toUpperCase().indexOf(value) != -1 || t.symbol.toUpperCase().indexOf(value) != -1)));
    };

    return (
        <Dialog
            onClose={closeCurrencyDialog}
            TransitionComponent={DialogTransition}
            keepMounted
            maxWidth={'xs'}
            aria-labelledby="coin-dialog"
            open={isOpenCurrencyDialog}
        >
            <DialogTitle id="coin-dialog" onClose={closeCurrencyDialog}>
                Select a token
            </DialogTitle>
            <DialogContent>
                <Grid container>
                    <Grid item xs={12} className={'fcx-mb-20'}>
                        <TextField label="Search" variant="outlined" fullWidth size={'small'} className={'fcx-mt-10'} onChange={handleChange}/>
                    </Grid>
                    <Grid item xs={12}>
                        <MenuList className={classes.list}>
                            {
                                listOfTokens.map((token, i) => {
                                    return <MenuItem key={i} onClick={() => handleSubmit(token.symbol, token.address)} disabled={token.symbol === currencies.current}>
                                        <ListItemIcon>
                                            <img src={token.logoURI} className={classes.tokenIcon}/>
                                        </ListItemIcon>
                                          <Typography>{token.symbol}({token.name}){balance[token.address] && <><br/><Typography variant={'caption'}>{balance[token.address]}</Typography></>}</Typography>
                                    </MenuItem>;
                                })
                            }
                        </MenuList>
                    </Grid>
                </Grid>
            </DialogContent>
        </Dialog>
    );
};

const mapStateToProps = (state) => {
    return {
        currencies: state.currencies,
        dialog: state.dialog,
    };
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        closeCurrencyDialog: closeCurrencyDialog,
        changeCurrency: changeCurrency,
    }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(CoinChooseDialog);