import React, { useEffect, useMemo, useState } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import { makeStyles } from '@material-ui/core';
import Tooltip from '@material-ui/core/Tooltip';
import InfoIcon from '@material-ui/icons/Info';
import TextField from '@material-ui/core/TextField';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Slider from '@material-ui/core/Slider';
import Switch from '@material-ui/core/Switch';
import clsx from 'clsx';
import { connect } from 'react-redux';
import { openConnectAWalletDialog, openCurrencyDialog } from '../actions/dialogActions';
import { useTokenPriceUsd } from '../hooks/useTokenPriceUsd';
import { useGasPrice } from '../hooks/useGasPrice';
import { formatEther, formatUnits, parseUnits } from '@ethersproject/units';
import { DEFAULT_DEPOSIT_END_DATE, ETHEREUM_ADDRESS } from '../constants';
import { useWalletTokensBalance } from '../hooks/useWalletTokensBalance';
import Alert from '@material-ui/lab/Alert';
import { KeyboardDatePicker } from '@material-ui/pickers';
import moment from 'moment';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import InputAdornment from '@material-ui/core/InputAdornment';
import { useWeb3React } from '@web3-react/core';
import { estimateGasDeposit, estimateGasExpandDeposit, expandDeposit, findDepositByIndex, isActiveDeposit, makeDeposit } from '../deposit';
import { RampInstantSDK } from '@ramp-network/ramp-instant-sdk';
import { isETH } from '../utils';
import { changeCurrency } from '../actions/currencyActions';
import { useHistory } from 'react-router-dom';
import { useRampInfo } from '../hooks/useRampInfo';
import Container from '@material-ui/core/Container';
import CoinChooseDialog from './CoinChooseDialog';


const useStyles = makeStyles(() => ({
  sourceWallet: {
    background: '#fff',
    borderRadius: '10px 0 0 10px',
  },
  sourceCard: {
    background: '#fff',
    borderRadius: '0 10px 10px 0',
  },
  sourceActive: {
    color: '#0d4bdc',
    borderColor: '#0d4bdc',
    '&:hover': {
      borderColor: '#0d4bdc',
    },
  },
  infoIcon: {
    width: '13px',
    cursor: 'pointer',
  },
  tokenIcon: {
    maxWidth: '30px',
    margin: '5px 5px',
    borderRadius: '50px',
  },
}));

const marks = [
  {
    value: 0,
    label: '',
  },
  {
    value: 25,
    label: '25%',
  },
  {
    value: 50,
    label: '50%',
  },
  {
    value: 75,
    label: '75%',
  },
  {
    value: 100,
    label: '',
  },
];

const orInputLabels = [
  { label: 'Value', icon: '=' },
  { label: 'Multiply', icon: 'x' },
  { label: 'Percentage', icon: '%' },
];


const DepositComponent = ({ mod = 'newDeposit', currencies, openCurrencyDialog, depositIndex = -1, changeCurrency }) => {
    const classes = useStyles();
    const { active, account, library, chainId } = useWeb3React();
    const history = useHistory();

    const { current: currentToken, currentAddress: currentTokenAddress, list: tokenList } = currencies;
    const { balance } = useWalletTokensBalance(tokenList);
    const { price: ethPrice } = useTokenPriceUsd(ETHEREUM_ADDRESS);
    const { price } = useTokenPriceUsd(currentTokenAddress);
    const { gas } = useGasPrice();

    let currentTokenImg = null;
    const filteredToken = useMemo(() => tokenList.filter(t => t.symbol === currentToken), [currentToken]);
    if (filteredToken.length > 0) {
      currentTokenImg = filteredToken[0].logoURI;
    }

    const currentTokenBalance = balance[currentTokenAddress];
    const [source, setSource] = useState(active ? 'wallet' : 'card');
    const [cryptoValue, setCryptoValue] = useState(0);
    const [cryptoValueUsd, setCryptoValueUsd] = useState(0);
    const [sliderValue, setSliderValue] = useState(100);
    const [isOverWallet, setOverWallet] = useState(false);
    const [transactionGasCost, setTransactionGasCost] = useState(-1);

    const [endDate, setEndDate] = useState(moment().add(DEFAULT_DEPOSIT_END_DATE, 'months'));

    const [isOrSwitch, setOrSwitch] = useState(false);
    const [orVariant, setOrVariant] = useState(0);
    const [orTarget, setOrTarget] = useState(0);
    const [orTargetPrice, setTargetPrice] = useState(0);
    const [isNeedToRecalculateTargetPrice, setNeedToRecalculateTargetPrice] = useState(false);

    const [isMinimalError, setMinimalError] = useState(false);
    const [isMaxError, setMaxError] = useState(false);

    const { isSupported, minPurchaseCryptoAmount, maxSinglePurchase, maxFeePercent, decimals, networkFeeEur } = useRampInfo(currentToken);

    const handleCryptoChange = (e) => {
      setCryptoValue(e.target.value);
    };
    const handleSliderChange = (e, v) => {
      setSliderValue(v);
    };

    const handleSliderChangeDone = (e, v) => {
      if (currentTokenBalance) {
        setCryptoValue(v * currentTokenBalance / 100);
      }
    };

    const isActive = (val) => {
      return source === val;
    };

    useEffect(() => {
      if (!isOrSwitch && price) {
        setOrTarget(parseFloat(price));
        setTargetPrice(parseFloat(price));
      }
    }, [price]);

    useEffect(async () => {
      if (depositIndex > -1 && tokenList.length > 0) {
        const deposit = await findDepositByIndex(library, account, chainId, depositIndex);
        if (deposit) {
          const { token } = deposit;
          if (isActiveDeposit(deposit)) {
            const t = tokenList.filter(t => t.address === token)[0];
            changeCurrency(t.symbol, token);
          } else {
            console.log('empty deposit');
          }
        } else {
          console.log('can\'t find deposit');
        }
      }
    }, [depositIndex, tokenList]);

    useEffect(() => {
      if (currentTokenBalance) {
        const b = parseFloat(currentTokenBalance);
        setCryptoValue(b);
        setSliderValue(100);
        setCryptoValueUsd(b * price);
      }
    }, [currentTokenBalance]);


    useEffect(() => {
      if (cryptoValue) {
        setCryptoValueUsd(cryptoValue * price);
      } else {
        setCryptoValueUsd(0);
      }
    }, [price]);

    useEffect(() => {
      if (cryptoValue) {
        let slider = 200;
        if (currentTokenBalance && parseFloat(currentTokenBalance) > 0) {
          slider = 100 * cryptoValue / parseFloat(currentTokenBalance);
        }
        setOverWallet(slider > 100);
        setSliderValue(slider);
        setCryptoValueUsd(cryptoValue * price);

        if (isActive('card') && isSupported) {
          if (cryptoValue < parseFloat(formatUnits(minPurchaseCryptoAmount, decimals))) {
            setMinimalError(true);
          } else {
            setMinimalError(false);
          }
          if (maxSinglePurchase != -1 && cryptoValueUsd > maxSinglePurchase) {
            setMaxError(true);
          } else {
            setMaxError(false);
          }
        } else {
          setMaxError(false);
          setMinimalError(false);
        }

      } else {
        setCryptoValueUsd(0);
      }
      if (mod === 'newDeposit') {
        estimateGasDeposit(currentToken, currentTokenAddress, cryptoValue, endDate, isOrSwitch, orTargetPrice, account, library, chainId).then(res => {
          setTransactionGasCost(res);
        }).catch(e => {
          setTransactionGasCost(e);
        });
      } else {
        estimateGasExpandDeposit(account, library, chainId, currentTokenAddress, currentToken, depositIndex, cryptoValue).then(res => {
          setTransactionGasCost(res);
        }).catch(e => {
          setTransactionGasCost(e);
        });
      }
    }, [cryptoValue]);

    useEffect(() => {
      if (orTarget) {
        if (isNeedToRecalculateTargetPrice) {
          switch (orVariant) {
            case 0:
              setTargetPrice(parseFloat(orTarget));
              break;
            case 1:
              setTargetPrice(price * parseFloat(orTarget));
              break;
            case 2:
              setTargetPrice(price * (parseFloat(orTarget) / 100));
              break;
          }
        }
      } else {
        setTargetPrice(0);
      }
      setNeedToRecalculateTargetPrice(false);
    }, [orTarget]);

    const handleSetVariant = (variant) => {
      switch (variant) {
        case 0:
          setOrTarget(parseFloat(orTargetPrice).toFixed(2));
          break;
        case 1:
          setOrTarget(parseFloat(orTargetPrice / price).toFixed(2));
          break;
        case 2:
          setOrTarget(parseFloat((orTargetPrice / price) * 100).toFixed(2));
          break;
      }
      setOrVariant(variant);
    };


    const handleDeposit = () => {
      if (isActive('wallet')) {
        if (mod === 'newDeposit') {
          makeDeposit(currentToken, currentTokenAddress, cryptoValue, endDate, isOrSwitch, orTargetPrice, account, library, chainId).then(res => {
            if (res) {
              history.push('/');
            }
          }).catch(e => {
            console.error(e);
          });
        } else {
          expandDeposit(account, library, chainId, currentTokenAddress, currentToken, depositIndex, cryptoValue).then(res => {
            if (res) {
              history.push('/');
            }
          }).catch(e => {
            console.error(e);
          });
        }
      } else {

        //

        if (mod === 'newDeposit') {
          const widget = new RampInstantSDK({
            hostAppName: 'Crypto Freezer',
            hostLogoUrl: 'https://cf.greenlogic.tech/wallets/metamask.png',
            variant: 'auto',
            fiatCurrency: 'USD',
            fiatValue: cryptoValueUsd,
            // finalUrl: 'https://cf.greenlogic.tech',
            swapAmount: isETH(currentTokenAddress) ? parseUnits('' + cryptoValue, 18) : parseUnits('' + cryptoValue, 8),
            // userAddress: '0x6C4a1CC11dC83771d79E4e3b26511f7b3aA23468'
            swapAsset: currentToken,
            url: 'https://ri-widget-staging.firebaseapp.com',
            // variant: 'embedded-desktop',
            // containerNode: document.getElementById('ramp'),
          });
          widget.show();
          widget.domNodes.overlay.style.zIndex = 10000;
        } else {
          //expand
          const widget = new RampInstantSDK({
            hostAppName: 'Crypto Freezer',
            hostLogoUrl: 'https://cf.greenlogic.tech/wallets/metamask.png',
            variant: 'auto',
            fiatCurrency: 'USD',
            fiatValue: cryptoValueUsd,
            // finalUrl: 'https://cf.greenlogic.tech',
            swapAmount: isETH(currentTokenAddress) ? parseUnits('' + cryptoValue, 18) : parseUnits('' + cryptoValue, 8),
            // userAddress: '0x6C4a1CC11dC83771d79E4e3b26511f7b3aA23468'
            swapAsset: currentToken,
            url: 'https://ri-widget-staging.firebaseapp.com',
            // variant: 'embedded-desktop',
            // containerNode: document.getElementById('ramp'),
          });
          widget.show();
          widget.domNodes.overlay.style.zIndex = 10000;
        }
      }
    };

    return (
      <Container maxWidth="sm">
        <Grid container>
          <Grid item xs={12}>
            <Typography component={'h1'} variant={'h3'} className={'p-align-center fcx-mt-10 fcx-mb-20'}>{mod === 'newDeposit' ? 'NEW DEPOSIT' : 'EXPAND DEPOSIT'}</Typography>
          </Grid>
          <Grid item xs={6} className={'fcx-mb-10'}><Button variant="outlined" className={clsx(isActive('wallet') && classes.sourceActive, classes.sourceWallet)} fullWidth onClick={() => setSource('wallet')} disabled={!active}>From Wallet</Button></Grid>
          <Grid item xs={6} className={'fcx-mb-10'}><Button variant="outlined" className={clsx(isActive('card') && classes.sourceActive, classes.sourceCard)} fullWidth onClick={() => setSource('card')}>From Credit Card</Button></Grid>
          <Grid item xs={12}>
            <Card className={'w-100 fcx-px-30 fcx-py-20 fcx-round-10 p-relative fcx-z-10'} elevation={5}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  {isActive('card') && !isSupported && <Alert severity="error" className={'fcx-mb-20'}>Token isn&apos;t supported by Ramp</Alert>}
                  {isActive('card') && isSupported && isMinimalError && <Alert severity="error" className={'fcx-mb-20'}>Minimum amount {formatUnits(minPurchaseCryptoAmount || 0, decimals)} {currentToken}</Alert>}
                  {isActive('card') && isSupported && isMaxError && <Alert severity="error" className={'fcx-mb-20'}>Max amount {maxSinglePurchase} USD</Alert>}
                  {isActive('wallet') && isOverWallet && <Alert severity="error" className={'fcx-mb-20'}>The value exceeds wallet balance</Alert>}
                </Grid>
                <Grid item xs={5}><Typography variant={'h6'} className={'f-bold'}>CRYPTO</Typography></Grid>
                <Grid item xs={2}></Grid>
                <Grid item xs={5} className={'p-align-right'}><Typography variant={'caption'}>current {currentToken} price: ${price}</Typography></Grid>
                <Grid item xs={5}>
                  <TextField
                    InputProps={{
                      inputProps: {
                        min: 0,
                      },
                    }}
                    variant="outlined"
                    value={cryptoValue}
                    type="number"
                    fullWidth
                    onChange={handleCryptoChange}
                  />
                </Grid>
                <Grid item xs={2}></Grid>
                <Grid item xs={5}>
                  <Tooltip title={currentTokenBalance + ' ' + currentToken} disableFocusListener={!currentTokenBalance} disableTouchListener={!currentTokenBalance} disableHoverListener={!currentTokenBalance}>
                    <Button
                      fullWidth
                      variant="contained"
                      disableElevation={true}
                      disabled={mod !== 'newDeposit'}
                      endIcon={mod === 'newDeposit' && <ExpandMoreIcon/>}
                      onClick={mod === 'newDeposit' ? openCurrencyDialog : () => {
                      }}
                    >
                      <img src={currentTokenImg} className={classes.tokenIcon}/>{currentToken}
                    </Button>
                  </Tooltip>
                </Grid>
                {
                  isActive('wallet') ? <>
                      <Grid item xs={12}>
                        <Slider
                          value={sliderValue}
                          aria-labelledby="discrete-slider"
                          valueLabelDisplay={'auto'}
                          valueLabelFormat={(v) => parseInt(v) + '%'}
                          onChangeCommitted={handleSliderChangeDone}
                          onChange={handleSliderChange}
                          step={1}
                          marks={marks}
                          min={0}
                          max={100}/>
                      </Grid>
                      <Grid item xs={12} className={'p-align-center fcx-mb-20'}><Typography variant={'caption'} className={'f-bold'}>WALLET:&nbsp;&nbsp;&nbsp;{parseFloat(currentTokenBalance - cryptoValue).toFixed(2)} {currentToken}</Typography></Grid>
                    </>
                    :
                    <>
                      <Grid item xs={12} className={'p-align-center fcx-pt-10 fcx-pb-5 fcx-mb-20'}><Typography variant={'h1'}>{cryptoValueUsd.toFixed(2)}$</Typography></Grid>
                    </>
                }
                {mod === 'newDeposit' && <>
                  <Grid item xs={12}>
                    <Typography variant={'h6'} className={'f-bold fcx-pb-5'}>UNFREEZE WHEN</Typography>
                  </Grid>
                  <Grid container direction="row" justify="center" alignItems="center">
                    <Grid item xs={5} className={'p-align-center h-100'}>
                      <Button className={'b-light-blue h-100'} fullWidth={true} disableRipple={true}>END DATE</Button>
                    </Grid>
                    <Grid item xs={2} className={'p-align-center'}><Button>=</Button></Grid>
                    <Grid item xs={5}>
                      <KeyboardDatePicker
                        value={endDate}
                        format="DD/MM/yyyy"
                        disablePast
                        autoOk
                        variant="inline"
                        inputVariant="outlined"
                        label="End date"
                        minDate={moment().add(1, 'days')}
                        onChange={(v) => setEndDate(v)}
                      />
                    </Grid>
                  </Grid>
                  <Grid item xs={12}><Divider className={'fcx-mt-10 fcx-mb-5'}/></Grid>
                  <Grid item xs={6}><strong className={'fcx-mr-5'}>OR</strong><Switch color={'primary'} inputProps={{ 'aria-label': 'primary checkbox' }} value={isOrSwitch} onChange={(e, v) => {
                    setOrSwitch(v);
                    setOrVariant(0);
                  }}/></Grid>
                  <Grid item xs={6} className={'p-align-right'}>
                    <ButtonGroup color="primary" aria-label="outlined primary button group">
                      <Button size="small" variant={clsx(orVariant === 0 && 'contained')} disabled={!isOrSwitch} onClick={() => handleSetVariant(0)}>value</Button>
                      <Button size="small" variant={clsx(orVariant === 1 && 'contained')} disabled={!isOrSwitch} onClick={() => handleSetVariant(1)}>x</Button>
                      <Button size="small" variant={clsx(orVariant === 2 && 'contained')} disabled={!isOrSwitch} onClick={() => handleSetVariant(2)}>%</Button>
                    </ButtonGroup>
                  </Grid>
                  <Grid container direction="row" justify="center" alignItems="center">
                    <Grid item xs={5} className={'p-align-center h-100'}>
                      <Button className={'b-light-blue h-100'} fullWidth={true} disableRipple={true} disabled={!isOrSwitch}>TARGET PRICE</Button>
                    </Grid>
                    <Grid item xs={2} className={'p-align-center'}><Button disabled={!isOrSwitch}>&gt;=</Button></Grid>
                    <Grid item xs={5}>
                      <TextField type="number"
                                 fullWidth={true}
                                 InputProps={{
                                   startAdornment: <InputAdornment position="start">{orInputLabels[orVariant].icon}</InputAdornment>,
                                   inputProps: {
                                     min: 0,
                                   },
                                 }}
                                 disabled={!isOrSwitch}
                                 label={orInputLabels[orVariant].label}
                                 variant="outlined"
                                 value={orTarget}
                                 onChange={(e) => {
                                   setNeedToRecalculateTargetPrice(true);
                                   setOrTarget(e.target.value);
                                 }}
                      />
                    </Grid>
                  </Grid>
                  <Grid item xs={12} className={'fcx-mt-5'}>
                    <Typography variant={'caption'} className={isOrSwitch == true ? ' ' : 'fcx-opacity-2'}>target {currentToken} price: ${orTargetPrice.toFixed(2)}</Typography>
                  </Grid>
                </>
                }
                <Grid item xs={12} className={'p-align-center fcx-mb-10'}>
                  <Button variant={'contained'} color={'primary'} disabled={(cryptoValue === 0 || isActive('wallet') && isOverWallet) || (isActive('card') && !isSupported)} onClick={handleDeposit}>{mod === 'newDeposit' ? 'MAKE DEPOSIT' : 'EXPAND DEPOSIT'}</Button>
                </Grid>
              </Grid>
            </Card>
            <Box className={'b-gray fcx-mx-30 fcx-round-5-bottom fcx-pt-5 fcx-pb-10 fcx-px-20 fcx-round-5 p-relative fcx-z-0'}>
              <Typography variant={'caption'}>
                <Grid container spacing={2}>
                  {
                    isActive('wallet') && <Grid item xs={12}>
                      {transactionGasCost != '-1' && <><Tooltip title={'Lorem Ipsum is simply dummy text of the'}><InfoIcon className={classes.infoIcon}/></Tooltip><b>gas cost: {parseFloat(formatEther(transactionGasCost * gas)).toFixed(5)} ETH $ {(formatEther(transactionGasCost * gas) * ethPrice).toFixed(2)}</b></>}
                    </Grid>
                  }
                  {isActive('card') && <Grid item xs={12}>
                    {
                      isSupported && minPurchaseCryptoAmount && <><Tooltip title={'Lorem Ipsum is simply dummy text of the'}><InfoIcon className={classes.infoIcon}/></Tooltip><b>Ramp Min deposit: {formatUnits(minPurchaseCryptoAmount || 0, decimals)} {currentToken}</b><br/></>
                    }
                    {
                      isSupported && maxSinglePurchase != '-1' && <><Tooltip title={'Lorem Ipsum is simply dummy text of the'}><InfoIcon className={classes.infoIcon}/></Tooltip><b>Ramp Max deposit: {maxSinglePurchase} USD</b><br/></>
                    }
                    {
                      isSupported && <><Tooltip title={'Lorem Ipsum is simply dummy text of the'}><InfoIcon className={classes.infoIcon}/></Tooltip><b>Ramp Max Fee: {maxFeePercent} %</b><br/></>
                    }
                    {
                      isSupported && <><Tooltip title={'Lorem Ipsum is simply dummy text of the'}><InfoIcon className={classes.infoIcon}/></Tooltip><b>Network Fee: {networkFeeEur} EUR</b></>
                    }
                  </Grid>
                  }
                  <Grid item xs={12}><Divider/></Grid>
                  <Grid item xs={12}>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. </Grid>
                </Grid>
              </Typography>
            </Box>
          </Grid>
        </Grid>
        <CoinChooseDialog balance={balance}/>
      </Container>
    );
  }
;

const mapStateToProps = (state) => ({
  currencies: state.currencies,
});


const mapDispatchToProps = {
  openCurrencyDialog,
  openConnectAWalletDialog,
  changeCurrency,
};

export default connect(mapStateToProps, mapDispatchToProps)(DepositComponent);