import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import { DialogTransition } from './common/DialogTransition';
import DialogTitle from './common/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Grid from '@material-ui/core/Grid';
import { bindActionCreators } from 'redux';
import { closeStatusDialog, STATUS_SUCCESS } from '../actions/dialogActions';
import { connect } from 'react-redux';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import CancelIcon from '@material-ui/icons/Cancel';

const TransactionInProgressDialog = (props) => {

  const { isOpenStatusDialog, statusInfo, closeStatusDialog } = props;
  const { status, title, content } = statusInfo;

  return <Dialog
    onClose={closeStatusDialog}
    TransitionComponent={DialogTransition}
    keepMounted
    maxWidth={'xs'}
    fullWidth={true}
    aria-labelledby="simple-dialog-title"
    open={isOpenStatusDialog}
  >
    <DialogTitle id="customized-dialog-title" onClose={closeStatusDialog}>
      {title}
    </DialogTitle>
    <DialogContent>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          {status === STATUS_SUCCESS ? <CheckCircleIcon/> : <CancelIcon/>}
        </Grid>
        <Grid item xs={12}>
          {content}
        </Grid>
      </Grid>
    </DialogContent>
  </Dialog>;
};

const mapStateToProps = (state) => {
  return state.dialog;
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    closeStatusDialog: closeStatusDialog,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(TransactionInProgressDialog);