import React from 'react';
import { Box, makeStyles, withStyles } from '@material-ui/core';
import clsx from 'clsx';
import Tooltip from '@material-ui/core/Tooltip';

const colors = ['#30B66D', '#3363D7', '#201D1D', '#EC651E', '#D265E1'];

const useStyles = makeStyles(() => ({
  chart: {
    backgroundColor: 'lightgreen',
    borderRadius: '5px !important',
    overflow: 'hidden',
  },
  box: {
    cursor: 'pointer',
  },
}));

const styles = {
  root: {
    backgroundColor: (props) => props.bg,
  },
};

const Bar = withStyles(styles)(Box);

const StackedChart = ({ data }) => {
  const classes = useStyles();
  return (
    <Box width="100%">
      <Box display="flex" flexDirection="row">
        {data.map((e, i) => {
          const title = '$' + parseFloat(e.value).toFixed(6) + '\n' + parseFloat(e.percentage).toFixed(6) + '%';
          return (
            <Box key={'label-' + i} display="flex" justifyContent="center" alignItems="flex-end" width={e.percentage + '%'} className={clsx('f-normal')}>
              <Tooltip title={<span style={{ whiteSpace: 'pre-line' }}>{title}</span>}><span className={classes.box}>{e.name}</span></Tooltip>
            </Box>
          );
        })}
      </Box>
      <Box display="flex" flexDirection="row" height="7px" className={classes.chart}>
        {data.map((e, i) => {
          return <Bar key={'piece-' + i} bg={colors[i]} height="7px" width={e.percentage + '%'}></Bar>;
        })}
      </Box>
    </Box>
  );
};

export default StackedChart;
