import { useEffect, useState } from 'react';
import { formatEther, formatUnits } from '@ethersproject/units';
import { getProviderOrSigner } from '../utils';
import { ERC20_ABI_INTERFACE, ETHEREUM_ADDRESS } from '../constants';
import { useWeb3React } from '@web3-react/core';

export function useWalletTokensBalance(tokenList) {
  const [balance, setBalance] = useState({});
  const { active, account, library, chainId, connector } = useWeb3React();

  const prepareCall = (providerOrSigner, tokenList, call) => {
    const filteredTokens = tokenList.filter(t => t.address !== ETHEREUM_ADDRESS);
    const promises = filteredTokens.map(t => {
      return providerOrSigner.call({
        to: t.address,
        data: call,
      });
    });
    return { tokens: filteredTokens, promises: Promise.all(promises) };
  };

  //balanceOf
  useEffect(async () => {
    if (active && account) {
      const result = {};
      const eth = await library.getBalance(account);
      const func = ERC20_ABI_INTERFACE.getFunction('balanceOf');
      const call = ERC20_ABI_INTERFACE.encodeFunctionData(func, [account]);
      const providerOrSigner = getProviderOrSigner(library, account);
      const { tokens, promises } = prepareCall(providerOrSigner, tokenList, call);
      const res = await promises;
      result[ETHEREUM_ADDRESS] = formatEther(eth);
      tokens.forEach((t, i) => {
        let b = '0.0';
        if (res[i] !== '0x') {
          const decode = ERC20_ABI_INTERFACE.decodeFunctionResult(func, res[i]);
          b = formatUnits(decode.balance, 8);
        }
        result[t.address] = b;
      });
      setBalance(result);
    }
  }, [active, account, library, chainId, connector]);
  return { balance: balance };
}