import FileCopyIcon from '@material-ui/icons/FileCopy';
import Button from '@material-ui/core/Button';
import React, { useEffect, useState } from 'react';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';

const CopyAddressButton = ({ address }) => {
  const [isCopied, setCopied] = useState(false);

  useEffect(() => {
    if (isCopied) {
      setTimeout(() => setCopied(!isCopied), 500);
    }
  }, [isCopied]);

  const handleCopy = () => {
    navigator.clipboard.writeText(address);
    setCopied(true);
  };

  return (<Button size={'small'} className={'f-dark-gray f-normal'} startIcon={!isCopied ? <FileCopyIcon/> : <CheckCircleOutlineIcon/>} onClick={handleCopy}>Copy address</Button>);
};
export default CopyAddressButton;