import { CHANGE_CURRENCY, LOAD_CURRENCIES } from '../actions/currencyActions';
import { ETHEREUM_ADDRESS } from '../constants';

const initState = {
  current: 'ETH',
  currentAddress: ETHEREUM_ADDRESS,
  list: [],
};

const currencyReducer = (state = initState, action) => {
  switch (action.type) {
    case LOAD_CURRENCIES: {
      return { ...state, list: action.payload };
    }
    case CHANGE_CURRENCY: {
      return { ...state, current: action.payload.current, currentAddress: action.payload.currentAddress };
    }
  }
  return state;
};

export default currencyReducer;