import { NEW_TRANSACTION, TRANSACTION_PROCESSED } from '../actions/transactionActions';
import _ from 'lodash';

const initState = {
  transactions: [],
};

const transactionReducer = (state = initState, action) => {
  switch (action.type) {
    case NEW_TRANSACTION: {
      return { ...state, transactions: [...state.transactions, action.payload] };
    }
    case TRANSACTION_PROCESSED: {
      console.log(action);
      const clone = _.cloneDeep(state.transactions);
      const { status, result } = action.payload;
      const results = {
        ...state, transactions: clone.map(t => {
          if (t.tx === action.payload.tx) {
            return { ...t, status: status, result: result };
          }
          return t;
        }),
      };
      return results;
    }
  }
  return state;
};

export default transactionReducer;