import React from 'react';
import { ThemeProvider } from '@material-ui/core/styles';
import getTheme from './base';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import responsiveFontSizes from "@material-ui/core/styles/responsiveFontSizes";

const AppThemeProvider = (props) => {
  // eslint-disable-next-line react/prop-types
  const { children, currentTheme } = props;
  const theme = getTheme(currentTheme);
  const themeWithResponsiveFonts = responsiveFontSizes(theme);
  return (
    <ThemeProvider theme={themeWithResponsiveFonts}>{children}</ThemeProvider>
  );
};

const mapStateToProps = (state) => {
  return state.theme;
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(AppThemeProvider);
