import React, { useEffect, useState } from 'react';
import { Box, Collapse, Grid, Hidden, LinearProgress, makeStyles, Typography, withStyles } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import { isETH } from '../utils';
import { formatEther, formatUnits } from '@ethersproject/units';
import { connect } from 'react-redux';
import moment from 'moment';
import { isMinPriceIsMax } from '../utils/wallet';
import clsx from 'clsx';
import NarrowButton from './NarrowButton';
import DashboardBox from './DashboardBox';
import CurrencyChart from './CurrencyChart';
import Divider from '@material-ui/core/Divider';
import DepositItemEvents from './DepositItemEvents';
import { tokenHistory } from '../utils/cachingAxios';
import CircularProgress from '@material-ui/core/CircularProgress';
import Tooltip from '@material-ui/core/Tooltip';

const useStyles = makeStyles(() => ({
  main: {
    padding: '5px 5px',
  },
  span: {
    marginLeft: '5px',
  },
  red: {
    color: 'red !important',
  },
}));

const BorderLinearProgress = withStyles(() => ({
  root: {
    height: 7,
    borderRadius: 5,
  },
  colorPrimary: {
    backgroundColor: '#CEDEE5',
  },
  bar: {
    borderRadius: 5,
    backgroundColor: '#4E6BDC',
  },

}))(LinearProgress);

const DepositItem = ({ priceFetcherContract, deposit, currencies, handleWithdraw, disabled = false }) => {
  const classes = useStyles();
  const history = useHistory();

  const [isLoadAdditionalData, setIsLoadAdditionalData] = useState(true);
  const [currentPrice, setCurrentPrice] = useState(1);
  const [eventsWithHistoricalPrice, setEventsWithHistoricalPrice] = useState([]);
  const [historicalDepositValue, setHistoricalDepositValue] = useState(1);

  const { id, data, isUnlocked, events } = deposit;
  const { token, value, unlockTimeUTC, minPrice } = data;
  const { list: currencyList } = currencies;

  const isEth = isETH(token);

  const newDepositDate = events.filter((el) => el.name === 'NewDeposit')[0].timestamp;

  const fValue = isEth ? formatEther(value) : formatUnits(value, 8);

  const currentDepositValue = fValue * currentPrice;

  const p = 100 - (historicalDepositValue / currentDepositValue * 100);

  const createDate = moment.unix(events[0].timestamp);
  const targetDate = moment.unix(unlockTimeUTC.toNumber());

  const daysTotal = targetDate.diff(createDate, 'days');
  const daysCurrent = moment().diff(createDate, 'days');

  const dayProgress = daysTotal === 0 ? 100 : (daysCurrent * 100) / daysTotal;
  const dayProgressNorm = dayProgress < 0 ? 0 : dayProgress > 100 ? 100 : dayProgress;

  const targetPrice = formatUnits(minPrice, 8);
  const targetPriceProgress = currentPrice * 100 / targetPrice;
  const targetPriceProgressNorm = targetPriceProgress < 0 ? 0 : targetPriceProgress > 100 ? 100 : targetPriceProgress;


  const [isExpanded, setExpanded] = useState(false);

  function handleExpand(id) {
    history.push('/expand-deposit/' + id);
  }

  useEffect(async () => {
    const cp = await priceFetcherContract.currentPrice(token);
    const proms = events
      .filter(e => e.name !== 'Withdraw')
      .map(async (e) => {
        try {
          const r = await tokenHistory(e.coingecoId, moment.unix(e.timestamp).format('DD-MM-YYYY'));
          e.hPrice = r.data.market_data.current_price.usd;
        } catch {
          e.hPrice = 0;
        }
        return e;
      });
    const ewhp = await Promise.all(proms);
    setEventsWithHistoricalPrice(ewhp);
    setCurrentPrice(parseFloat(formatUnits(cp, 8)));
    setIsLoadAdditionalData(false);
  }, []);

  useEffect(() => {
    const hdv = eventsWithHistoricalPrice.map(e => e.hPrice * formatUnits(e.args.value, isEth ? 18 : 8)).reduce((a, b) => a + b, 0);
    setHistoricalDepositValue(hdv);
  }, [eventsWithHistoricalPrice]);


  return (
    <Grid key={'id-' + id} item xs={12}>
      <DashboardBox p={3} onClick={() => setExpanded(!isExpanded)}>
        <Grid container>
          <Grid container item xs={12}>
            <Grid container item xs={12} sm={5} alignItems="center" className={'fcx-my-5'}>
              <Tooltip title={fValue + ' ' + currencyList.filter((c) => c.address === token)[0].symbol}><Typography variant={'h2'}>
                {parseFloat(fValue).toFixed(6)} {currencyList.filter((c) => c.address === token)[0].symbol}
              </Typography></Tooltip>
            </Grid>
            <Hidden smUp>
              <Grid container item xs={12}>
                <Divider className={'fcx-my-10'}></Divider>
              </Grid>
            </Hidden>
            <Grid container item xs={12} sm={7} alignItems="center" justify="flex-end" className={'fcx-my-10'}>
              {!isMinPriceIsMax(minPrice) && (
                <>
                  <Grid item xs={5}>
                    <Box textAlign="right">
                      <Typography variant={'caption'} className={'d-block fcx-mb-5'}>TARGET PRICE: <span className={clsx('f-bold', classes.span)}>${parseFloat(targetPrice).toFixed(2)}</span></Typography>
                      {isLoadAdditionalData ? <CircularProgress size={20}/> : <BorderLinearProgress variant="determinate" value={targetPriceProgressNorm}/>}
                    </Box>
                  </Grid>
                  <Grid container item xs={2} justify="center" alignItems="center">
                    OR
                  </Grid>
                </>
              )}

              <Grid item xs={5}>
                <Box textAlign="right">
                  <Typography variant={'caption'} className={'d-block fcx-mb-5'}>TARGET DATE: <span className={clsx('f-bold', classes.span)}>{targetDate.format('DD/MM/yyyy')}</span></Typography>
                  <BorderLinearProgress variant="determinate" value={dayProgressNorm}/>
                </Box>
              </Grid>
            </Grid>
            <Hidden smUp>
              <Grid container item xs={12}>
                <Divider className={'fcx-my-10'}></Divider>
              </Grid>
            </Hidden>
          </Grid>
        </Grid>
        <Collapse in={isExpanded}>
          <Grid container>
            <Grid container item xs={12} sm={6} alignItems="center" className={'fcx-my-20'}>
              {isLoadAdditionalData ? <CircularProgress size={20}/> : <DepositItemEvents hdt={historicalDepositValue} isEth={isEth} isExpanded={isExpanded} events={eventsWithHistoricalPrice}/>}
            </Grid>
            <Hidden smDown>
              <Grid item sm={6}>
                <Box width="100%" display="flex" py={1} justifyContent="flex-end" pt={3}>
                  <CurrencyChart from={newDepositDate} symbol={currencyList.filter((c) => c.address === token)[0].symbol}/>
                </Box>
              </Grid>
            </Hidden>
          </Grid>
        </Collapse>
        <Hidden smUp>
          <Grid container item xs={12}>
            <Divider className={'fcx-my-10'}></Divider>
          </Grid>
        </Hidden>
        <Grid container item xs={12}>
          <Grid container item xs={5}>
            {!isUnlocked && (
              <Grid item xs={6} sm={5}>
                <Box pt={3} pb={1} alignItems="flex-end">
                  <NarrowButton onClick={() => handleExpand(id)} disabled={disabled}>
                    EXPAND<Hidden smDown> DEPOSIT</Hidden>
                  </NarrowButton>
                </Box>
              </Grid>
            )}
            {isUnlocked && (
              <Grid item xs={6} sm={6}>
                <Box pt={3} pb={1} ml={1} alignItems="flex-end">
                  <NarrowButton onClick={() => handleWithdraw(id)} disabled={disabled}>
                    WITHDRAW<Hidden smDown> DEPOSIT</Hidden>
                  </NarrowButton>
                </Box>
              </Grid>
            )}
          </Grid>
          <Grid container item xs={7} justify="flex-end" alignItems="flex-end">
            <Box pt={2} pb={1}>
              {isLoadAdditionalData ? <CircularProgress size={20}/> : <span>
                current deposit value:
                <span className={clsx('f-bold', classes.span)}>
                  ${parseFloat(currentDepositValue).toFixed(2)}
                  <Hidden xsDown>
                    <span className={clsx('f-green', classes.span, p < 0 && classes.red)}>{p > 0 && <>+</>}{parseFloat(p).toFixed(2)}%</span>
                  </Hidden>
                </span>
              </span>}
            </Box>
          </Grid>
        </Grid>
      </DashboardBox>
    </Grid>
  );
};

const mapStateToProps = (state) => ({
  currencies: state.currencies,
});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(DepositItem);
