import { NoEthereumProviderError, UserRejectedRequestError } from '@web3-react/injected-connector';
import Web3 from 'web3';
import { Arkane } from '@arkane-network/web3-arkane-provider';
import { AbstractConnector } from '@web3-react/abstract-connector';
import { ArkaneConnect, SecretType } from '@arkane-network/arkane-connect';


export class ArkaneConnector extends AbstractConnector {

  constructor(options) {
    super({ supportedChainIds: [1, 3, 4, 5, 42] });
    this.options = options;
    this.arkaneConnect = new ArkaneConnect(options.clientId, options);
    Arkane.createArkaneProviderEngine({ ...options, skipAuthentication: true }).then(provider => {
      this.provider = provider;
      window.web3 = new Web3(provider);
    });
  }

  async activate() {
    if (!window.web3.eth) {
      throw new NoEthereumProviderError();
    }
    let account;
    try {
      account = await this.arkaneConnect.api.getWallets({ secretType: SecretType.ETHEREUM }).then(response => {
          return response[0].address;
        },
      );
    } catch (error) {
      if ((error).code === 4001) {
        throw new UserRejectedRequestError();
      }
    }
    window.addEventListener('accountsChanged', () => {
      window.web3.eth.getAccounts().then(accounts => {
          if (accounts.length === 0) {
            super.emitDeactivate();
          } else {
            super.emitUpdate({ account: accounts[0] });
          }
        },
      );
    });
    return { provider: this.provider, ...(account ? { account } : {}) };
  }

  async getProvider() {
    return this.provider;
  }

  async deactivate() {
    if (window.web3 && window.web3.eth && window.web3.eth.removeListener) {
      window.removeListener('accountsChanged', this.handleAccountsChanged);
    }
    await this.arkaneConnect.logout();
    Arkane.arkaneConnect().logout();
  }

  async getChainId() {
    if (!window.web3 || !window.web3.eth) {
      throw new NoEthereumProviderError();
    }
    return window.web3.eth.getChainId();
  }

  async getAccount() {
    if (!window.web3 || !window.web3.eth) {
      throw new NoEthereumProviderError();
    }
    return await window.web3.eth.getAccounts().then(
      accounts => accounts[0],
    );
  }

  async isAuthorized() {
    return true
  }
}